import * as React from 'react';
import {AppPage}  from '../../sharedcomponents/page';
import {DynamicPage} from '../dynamicpage';
import {connect} from 'react-redux';

import './thematique.less';

export interface ThematiqueDetailPageProps{
    user? : States.IAppUserState;
    event? : States.ICurrentEventState;
    visualTheme? : States.IEventVisualThemeState;
    templates : States.IEventTemplatesState,
    thematiques : States.IEventThematiquesState,
    entities : States.IEventEntitiesState
}

export class ThematiqueDetailPageComponent extends React.Component<ThematiqueDetailPageProps, any> {
    render(){
        var thematiqueid = (this.props as any).params.thematiqueid;
        var thematique = this.props.thematiques.thematiques.filter((t) => t.id == thematiqueid)[0];
        var context = {
            thematique : thematique,
            entity : thematique,
            entitySchema : this.props.entities.schemas.thematique
        }
        return <DynamicPage template="thematique" context={context} user={this.props.user} event={this.props.event} visualTheme={ this.props.visualTheme } templates={this.props.templates} />
    }
}

function mapStateToProps(state: States.IAppState) {
    return { 
        user: state.user,
        event : state.event.current,
        templates : state.event.templates,
        thematiques : state.event.thematiques,
        entities : state.event.entities,
    }
}

export var ThematiqueDetailPage: new (any) => React.Component<ThematiqueDetailPageProps, any> = connect(mapStateToProps)(ThematiqueDetailPageComponent as any) as any;


