import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {CommonEditorParts} from './editor.common';
import {AppLabel} from '../../services/i18nservice';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';

export class SelectListEditor extends React.Component<any, any> {
    render(){
        return <div className={ "fieldeditor " + this.props.className }>
            <CommonEditorParts field={this.props.field} onChange={ this.props.onChange } />
            <ListValuesEditor field={this.props.field} onChange={ this.props.onChange } />
        </div>
    }
}

export class ListValuesEditor extends React.Component<any, any> {
    constructor(props){
        super(props);
         this.removeField =  this.removeField.bind(this);
        this.state = {
            pendingAdd : ""
        }
    }

    removeField(key, field){
        delete this.props.field.values[key];
        this.setState({});
    }

    addValue(){
        var code = this.state.pendingAdd;
        this.props.field.values = this.props.field.values || {};
        this.props.field.values[code] = {
            "fr" : code
        }
        this.setState({ pendingAdd : "" });
    }

    render(){
        var values;
        if (this.props.field.values){
            var keys = Object.keys(this.props.field.values);
            values = keys.map((k) => {
                return <ListValueEditor key={k} 
                    item={ this.props.field.values[k] } 
                    name={ k }
                    removeField={ () => this.removeField(k, this.props.field.values[k]) } />
            })
        }
        return <div className="fieldeditor-values">
            <AppLabel component="h4" i18n="entity.fields.values" />
            <ReactCSSTransitionGroup component="div"
                className="fieldvalues"
                transitionName="simplefadetransition"
                transitionEnterTimeout={200}
                transitionLeave={false}>
                {values}
            </ReactCSSTransitionGroup>
            <div className="addvalue">
                <input type="text" 
                    value={this.state.pendingAdd}
                    onChange={ (arg)=>{ this.setState({ pendingAdd : (arg.target as HTMLInputElement).value })}} />
                <button onClick={ this.addValue.bind(this) }><AppLabel i18n="actions.add" /></button>
            </div>
        </div>
    }
}

export class ListValueEditor extends React.Component<any, any> {
    render(){
        var item = this.props.item;
        var labels;
        var lngs = Object.keys(item);
        labels = lngs.map((lng) => {
            return <div key={lng} className="lnglabel"><label>{lng}</label><input type="text" value={ item[lng] } /></div>
        });
        return <div className="fieldbloc">
            <div className="valuecode">
                <input type="text" value={this.props.name} />
                <button className="linkbtn dripicons-trash" onClick={ this.props.removeField }></button>
            </div>
            <div className="valuelabels">
                {labels}
            </div>
        </div>
    }
}


