import * as React from 'react';
import * as ReactDOM from 'react-dom';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import {AppRoutes} from './app.routes';
import configureStore from './store';
import {Provider} from 'react-redux';
import { Router, Route, IndexRoute, browserHistory, hashHistory } from 'react-router';
import { syncHistoryWithStore } from 'react-router-redux';
import {actions as i18nActions} from './services/i18nservice';
import {actions as userActions} from './services/userservice';

import './styles/reset.less';
import './styles/transitions.less';


const usedHistory = hashHistory;
const store = configureStore(usedHistory, {});
const history = syncHistoryWithStore(usedHistory, store);

var approot = document.getElementById("approot");
ReactDOM.render(<Provider store={ store }>
      <AppRoutes history={history}/>
    </Provider>, approot);

i18nActions.loadLanguage("en")(store.dispatch);
userActions.checkUser()(store.dispatch);



