import { sessionsList } from '../../fakedata/sessions';

var INITIAL_STATE = <States.IEventSessionsState> {
    loading : true,
    sessions : []    
};

export function eventSessionsReducer(state = INITIAL_STATE, action = { type: '', payload: <any>null }) {
    switch (action.type) {
        case "EVENT_CURRENT": {
            return _.assign({}, state, { 
                loading : false,
                sessions : sessionsList
            });
        }

        default:
            return state;
    }
}

export var utils = {

}


