import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {DynamicBloc, DynamicContentBlocProps} from './common';

import './bloc.entityactions.less';

export class BlocEntityActions extends React.Component<DynamicContentBlocProps, any> {
    render(){
        var blocActions;

        if (this.props.context.session){
            blocActions = <SessionActions {...this.props} />
        } else if (this.props.context.speaker){
            blocActions = <SpeakerActions {...this.props} />
        } else if (this.props.context.exhibitor){
            blocActions = <ExhibitorActions {...this.props} />
        } else if (this.props.context.thematique){
            blocActions = <ThematiqueActions {...this.props} />
        } else if (this.props.context.participant){
            blocActions = <ParticipantActions {...this.props} />
        }

        if (blocActions){
            return <DynamicBloc {...this.props}>
                <div className="dynamiccontentbloc">
                    {blocActions}
                </div>
            </DynamicBloc>
        }else {
            return <div className="empty"></div>
        }
    }
}

export class SessionActions extends React.Component<DynamicContentBlocProps, any> {
    render(){
        return <div className="entityactions">
            <button>Ajouter dans mon agenda</button>
            <button>Ajouter aux favoris</button>
            <button>Poser une question</button>
        </div>
    }
}

export class SpeakerActions extends React.Component<DynamicContentBlocProps, any> {
    render(){
        return <div className="entityactions">
            <button>Envoyer un message</button>
            <button>Inviter sur Linkedin</button>
        </div>
    }
}

export class ThematiqueActions extends React.Component<DynamicContentBlocProps, any> {
    render(){
        return <div className="entityactions">
            <button>Ajouter aux favoris</button>
            <button>Poser une question</button>
        </div>
    }
}

export class ExhibitorActions extends React.Component<DynamicContentBlocProps, any> {
    render(){
        return <div className="entityactions">
            <button>Ajouter aux favoris</button>
            <button>Poser une question</button>
        </div>
    }
}

export class ParticipantActions extends React.Component<DynamicContentBlocProps, any> {
    render(){
        return <div className="entityactions">
            <button>Envoyer un message</button>
            <button>Inviter sur Linkedin</button>
        </div>
    }
}


