import { homePageTemplate } from '../../fakedata/templates.home';
import { thematiqueTemplate } from '../../fakedata/templates.thematique';
import { sessionTemplate } from '../../fakedata/templates.session';
import { speakerTemplate } from '../../fakedata/templates.speaker';
import { exhibitorTemplate } from '../../fakedata/templates.exhibitor';

var INITIAL_STATE = <States.IEventTemplatesState>{
    loading:true,
    templates : {}    
};

export function eventPagesReducer(state = INITIAL_STATE, action = { type: '', payload: <any>null }) {
    switch (action.type) {
        case "EVENT_CURRENT": {
            return _.assign({}, state, { 
                loading : false,
                templates : {
                    home : homePageTemplate,
                    thematique : thematiqueTemplate,
                    session : sessionTemplate,
                    speaker : speakerTemplate,
                    exhibitor : exhibitorTemplate
                } 
            });
        }

        default:
            return state;
    }
}

export var utils = {

}


