import * as React from 'react';
import {Link} from 'react-router';

import './front.appheader.less';

export interface FrontAppHeaderProps{
    event : States.ICurrentEventState;
    visualTheme : States.IEventVisualThemeState;
}

export class FrontAppHeader extends React.Component<FrontAppHeaderProps, any> {
    render(){
        var logo;
        var headerStyle = {};
        if (this.props.visualTheme && this.props.visualTheme.theme){
            if (this.props.visualTheme.theme.logo){
                logo = <Link to={ "/preview/" + this.props.event.id + "/" }><img src={this.props.visualTheme.theme.logo} /></Link>
            }

            headerStyle = { color: this.props.visualTheme.theme.accentColor, backgroundColor : this.props.visualTheme.theme.primaryColor };
        }
        
        return <div className="front-app-header" style={headerStyle}>
            <div className="logo">{logo}</div>
            <FrontAppHeaderMenu {...this.props} />
            <FrontAppHeaderItems  {...this.props} />
        </div>
    }
}

export class FrontAppHeaderMenu extends React.Component<FrontAppHeaderProps, any> {
    render(){
        return <div className="front-app-header-menu">
            <Link to={ "/preview/" + this.props.event.id + "/sessions"}>Sessions</Link>
            <Link to={ "/preview/" + this.props.event.id + "/speakers"}>Speakers</Link>
            <Link to={ "/preview/" + this.props.event.id + "/agenda"}>Mon agenda</Link>
        </div>
    }
}

export class FrontAppHeaderItems extends React.Component<FrontAppHeaderProps, any> {
    render(){
        return <div className="front-app-header-items">
            <Link to={ "/preview/" + this.props.event.id + "/chat"}><i className="dripicons-message"></i></Link>
            <Link to={ "/preview/" + this.props.event.id + "/profile"}><i className="dripicons-user"></i></Link>
        </div>
    }
}


