import * as $ from 'jquery';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import * as moment from 'moment';
import { Router, Route, IndexRoute, Link, browserHistory, hashHistory, useRouterHistory } from 'react-router';
import { AppShell } from './app.shell';

import { MyEventsPage } from './routes/myevents/myevents';
import { HomePage } from './routes/home/homepage';
import { SpeakersPage } from './routes/speakers/speakerspage';
import { SessionsPage } from './routes/sessions/sessionspage';
import { LoginPage } from './routes/login/loginpage';
import { DataGridPage } from './routes/datagridtests/datagridtests';
import { ContentEditor } from './routes/contenteditor/contenteditor';
import { EntitySchemaPage } from './routes/entityschema/entityschema';
import { HomePage as FrontHomePage } from './front/home/home';

import { FrontAppShell } from './front/front.appshell';
import { SessionsListPage } from './front/sessions/sessions';
import { SessionDetailPage } from './front/sessiondetail/sessiondetail';
import { SessionsAgendaPage } from './front/sessionsagenda/sessionsagenda';
import { SpeakersListPage } from './front/speakers/speakers';
import { SpeakerDetailPage } from './front/speakerdetail/speakerdetail';
import { ExhibitorsListPage } from './front/exhibitors/exhibitors';
import { ExhibitorDetailPage } from './front/exhibitordetail/exhibitordetail';
import { ChatPage } from './front/chat/chat';
import { ContentPage } from './front/contentpage/contentpage';
import { ProfilePage } from './front/profile/profile';
import { ContactPage } from './front/contact/contact';
import { ThematiqueDetailPage } from './front/thematique/thematique';

import { actions as userActions } from './services/userservice';
import { actions as eventActions } from './services/eventservice';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';

interface AppRoutesProps{
    user : States.IAppUserState,
    event : States.IEventState,
    history : any,
    userActions : typeof userActions;
    eventActions : typeof eventActions;
}


export class AppRoutesComponent extends React.Component<AppRoutesProps, any> {
    constructor(props){
        super(props);
        this.mustBeLogged = this.mustBeLogged.bind(this);
        this.mustHaveEvent = this.mustHaveEvent.bind(this);
    }

    mustBeLogged(nextState, replace){
        if (!this.props.user || !this.props.user.identity.login) {
            replace({
                pathname: '/login',
                state: { nextPathname: nextState.location.pathname }
            });
        }
    }

    mustHaveEvent(nextState, replace){
        if (!this.props.user || !this.props.user.identity.login) {
            replace({
                pathname: '/login',
                state: { nextPathname: nextState.location.pathname }
            });
            return;
        }
        else if (!this.props.event.current || !this.props.event.current.id) {
            if (nextState.params.eventid){
                var event = this.props.user.availableevents.filter((e) => {
                    return e.id == nextState.params.eventid;
                })[0];

                if (event){
                    this.props.eventActions.currentEvent(event);
                    return;
                }
            }

            replace({
                pathname: '/myevents',
                state: { nextPathname: nextState.location.pathname }
            });
            return;
        }
    }

    render(){
        return <Router history={ this.props.history }>
            <Route path="/" component={AppShell}>
                <IndexRoute onEnter={ this.mustBeLogged } component={MyEventsPage}/>
                <Route path="login" component={LoginPage} />
                <Route path="myevents" onEnter={ this.mustBeLogged } component={MyEventsPage} />       
                <Route path="/event/:eventid" onEnter={ this.mustHaveEvent }>
                    <IndexRoute component={HomePage}/>
                    <Route path="sessions" component={SessionsPage} />
                    <Route path="speakers" component={SpeakersPage} />
                    <Route path="grid" component={DataGridPage} />
                    <Route path="schema" component={EntitySchemaPage} />
                    <Route path="templates" component={ ContentEditor } />
                </Route> 
                      
            </Route>
            <Route path="/preview/:eventid" component={ FrontAppShell } onEnter={ this.mustHaveEvent }>
                <IndexRoute component={FrontHomePage}/>
                <Route path="agenda" component={SessionsAgendaPage} />
                <Route path="sessions" component={SessionsListPage} />
                <Route path="sessions/:sessionid" component={SessionDetailPage} />
                <Route path="speakers" component={SpeakersListPage} />
                <Route path="speakers/:speakerid" component={SpeakerDetailPage} />
                <Route path="exhibitors" component={ExhibitorsListPage} />
                <Route path="exhibitors/:exhibitorid" component={ExhibitorDetailPage} />
                <Route path="chat" component={ChatPage} />
                <Route path="chat/:contactid" component={ChatPage} />
                <Route path="content/:pageid" component={ContentPage} />
                <Route path="profile" component={ProfilePage} />
                <Route path="thematique/:thematiqueid" component={ThematiqueDetailPage} />
                <Route path="contact/:contactid" component={ContactPage} />
            </Route>              
        </Router>;
    }
}

function mapStateToProps(state : States.IAppState) {
    return { 
        user: state.user,
        event: state.event 
    }
}

function mapDispatchToProps(dispatch) {
  return {
    userActions: bindActionCreators(userActions, dispatch),
    eventActions: bindActionCreators(eventActions, dispatch)
  }
}

export var AppRoutes: new (any) => React.Component<any, any> = connect(mapStateToProps, mapDispatchToProps)(AppRoutesComponent as any) as any;


