import * as _ from 'lodash';
import * as React from 'react';
import {connect} from 'react-redux';
import {AppLabel} from '../services/i18nservice';
import './page.less';

export interface AppPageProps {
    defaultPadding? : boolean;
    scrollable? : boolean;
    withHeader? : boolean;
    className? : string;
}

export class AppPage extends React.Component<AppPageProps, any> {
    render(){
        var classes = ['app-page', this.props.className || ''];
        if (!(this.props.defaultPadding == false))
            classes.push("defaultPadding");
        if (!(this.props.withHeader == false))
            classes.push("withHeader");
        if (!(this.props.scrollable == false))
            classes.push("scrollable");
        return <div className={ classes.join(" ") }>
            {this.props.children}
        </div>
    }
}

export interface AppPageHeaderProps {
    i18n? : string;    
    title? : string;    
    inject? : any;
}

export class AppPageHeader extends React.Component<AppPageHeaderProps, any> {
    render(){        
        var title, actions;
        if (this.props.children){
            actions = <div className="actions">
                {this.props.children}
            </div>
        }

        if (this.props.i18n){
            title = <AppLabel component="h1" i18n={ this.props.i18n} inject={this.props.inject} />
        } else if (this.props.title){
            title = <h1>{ this.props.title }</h1>
        }

        return <header className="app-page-header">
            {title}
            {actions}
        </header>
    }
}



