import * as React from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {AppPage}  from '../../sharedcomponents/page';
import {utils} from '../../services/event.participantservice';
import {injectcontext} from '../../utils/decorators';
import './contact.less';

export interface ContactPageProps{
    participants : States.IParticipantsState;
    event: States.ICurrentEventState;
    thematiques: States.IEventThematiquesState;
    visualTheme : States.IEventVisualThemeState;
}

@injectcontext({ router : React.PropTypes.object })
export class ContactPageComponent extends React.Component<ContactPageProps, any> {
    constructor(props){
        super(props);
        this.chatWith = this.chatWith.bind(this);
        var contactid = (props as any).params.contactid;
        this.state = {
            contact : utils.byId(this.props.participants.allparticipants, contactid)
        }
    }

    chatWith(arg){
        var router = this.context["router"];
        router.push('/preview/' + this.props.event.id + '/chat/' + this.state.contact.id);
    }

    render(){
        var contact = this.state.contact as Entities.IParticipant;
        var contactPicStyle = {} as any;
        if (contact.photo) {
            contactPicStyle.backgroundImage = 'url("' + contact.photo + '")';
        }

        return <AppPage className="contactpage" withHeader={false} defaultPadding={false}>
            <div className="dynamiccontentblocwrapper">
                <div className="dynamiccontentbloc">
                    <div className="contactdesc">    
                        <div className="photo" style={contactPicStyle}></div>
                        <div className="details">        
                            <h2 style={{ color: this.props.visualTheme.theme.primaryColor }}>{contact.firstname + " " + contact.lastname}</h2>
                            <div className="description">
                                {contact.description || ""}
                            </div>
                            <div className="takecontact">
                                <button onClick={ this.chatWith }>Contacter {contact.firstname + " " + contact.lastname}</button>
                                <button>Inviter sur LinkedIn</button>
                            </div>
                        </div>
                        <div className="clearfix"></div>
                    </div>
                </div>
            </div>
            <ContactThematiques {...this.props} contact={this.state.contact} />
        </AppPage>
    }
}

function mapStateToProps(state: States.IAppState) {
    return {
        participants: state.event.participants,
        thematiques : state.event.thematiques
    }
}

export var ContactPage: new (any) => React.Component<any, any> = connect(mapStateToProps)(ContactPageComponent as any) as any;

interface ContactThematiquesProps extends ContactPageProps{
    contact : Entities.IParticipant
}

export class ContactThematiques extends React.Component<ContactThematiquesProps, any> {
    render(){
        var thematiques;
        if (this.props.contact && this.props.contact.thematiques){
            thematiques = this.props.contact.thematiques.map((t) => {
                return <ContactThematiqueItem key={ t.thematiqueId } {...this.props} thematique={t} />
            })
        }
        return <div className="contact-thematiques dynamiccontentblocwrapper">
            <div className="dynamiccontentbloc">
            	<h3>Ses centres d'intret</h3>
            	<div className="thematiques-items">
            	    {thematiques}
            	</div>
            </div>
        </div> 
    }
}

interface ContactThematiqueItemProps extends ContactThematiquesProps {
    thematique : Entities.IAppUserThematique;
}

export class ContactThematiqueItem extends React.Component<ContactThematiqueItemProps, any> {
    render(){        
        var rootTheme = this.props.thematiques.thematiques.filter((t) => t.id == this.props.thematique.thematiqueId)[0];
        return <div className="contact-thematique">
            <h4>{rootTheme.title}</h4>
            <ContactThematiqueActions {...this.props} rootTheme={rootTheme} title="il recherche" items={this.props.thematique.searching} />
            <ContactThematiqueActions {...this.props} rootTheme={rootTheme} title="il propose" items={this.props.thematique.proposing} />
        </div> 
    }
}

interface ContactThematiqueActionsProps extends ContactThematiqueItemProps {
    rootTheme : Entities.IEventThematique;
    title : string;
    items : string[];
}
export class ContactThematiqueActions extends React.Component<ContactThematiqueActionsProps, any> {
    render(){
        var actions;
        if (this.props.items){
            actions = this.props.items.map((actId) => {
                var act = this.props.thematiques.thematiquesActions.filter((a) => a.id == actId)[0];
                return <div key={actId} className="action">{act.title}</div>
            });
        }        
        return <div className="contact-thematique-actions">
            <h5>{ this.props.title }</h5>
            <div className="actions">
            {actions}
            </div>
        </div> 
    }
}


