import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {connect} from 'react-redux';
import {injectcontext} from '../../utils/decorators';
import {DynamicBloc, BlocTitle, IDynamicContentItemsBlocProperties,DynamicContentBlocProps} from './common';
import * as Slider from 'react-slick';

import './bloc.speakers.less';

interface BlocsSpeakersProps extends DynamicContentBlocProps {
    speakers : States.IEventSpeakersState,
    visualState : States.IAppVisualState
}

class BlocSpeakersComponent extends React.Component<BlocsSpeakersProps, any> {
    render() {
        var properties = (this.props.template.properties || {}) as IDynamicContentItemsBlocProperties;
        if (properties.itemsDisplayMode == "carousel"){
            return <BlocSpeakersCarousel {...this.props}  speakers={this.props.speakers} />
        } else if (properties.itemsDisplayMode == "list"){
            return <BlocSpeakersList {...this.props} speakers={this.props.speakers} />
        } else {
            return <BlocSpeakersList {...this.props}  speakers={this.props.speakers} />
        }
    }
}

function mapStateToProps(state : States.IAppState) {
    return { 
        speakers: state.event.speakers,
        visualState : state.visualstate
    }
}

export var BlocSpeakers: new (any) => React.Component<any, any> = connect(mapStateToProps)(BlocSpeakersComponent as any) as any;

class BlocSpeakersList extends React.Component<BlocsSpeakersProps, any> {
    render() {
        var speakers;
        if (this.props.event && this.props.speakers) {
            var list = this.props.speakers.speakers;
            if (this.props.template.properties.maxItems) {
                list = list.slice(0, this.props.template.properties.maxItems);
            }

            speakers = list.map((s) => {
                var speaker = s;
                return <BlocSpeakersItem speaker={s} event={ this.props.event } key={speaker.id} />;
            })
        }

        return <DynamicBloc {...this.props}><div className={"bloc-speakers list " + this.props.className }>
            <BlocTitle  {...this.props} />
            <div className="bloc-speakers-items bloc-content">
                {speakers}
            </div>
        </div>
        </DynamicBloc>
    }
}

class BlocSpeakersCarousel extends React.Component<BlocsSpeakersProps, any> {
    render() {
        var settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: Math.min(this.props.template.properties.maxItems, 5),
            slidesToScroll: Math.min(this.props.template.properties.maxItems, 5)
        };

        if (!(this.props.visualState.states.indexOf("S") >= 0)){
            settings.slidesToShow = 2;
            settings.slidesToScroll = 2;
        } else if (!(this.props.visualState.states.indexOf("M") >= 0)){
            settings.slidesToShow = 3;
            settings.slidesToScroll = 3;
        }else if (!(this.props.visualState.states.indexOf("L") >= 0)){
            settings.slidesToShow = 4;
            settings.slidesToScroll = 4;
        }

        var speakers;
        if (this.props.event && this.props.speakers) {
            var list = this.props.speakers.speakers;
            if (this.props.template.properties.maxItems) {
                list = list.slice(0, this.props.template.properties.maxItems);
            }

            speakers = list.map((s) => {
                var speaker = s;
                return <div className="speakerslide" key={speaker.id}>
                    <BlocSpeakersItem speaker={s} event={ this.props.event } />
                </div>
            })
        }

        return <DynamicBloc {...this.props}><div className={"bloc-speakers carousel " + this.props.className }>
            <BlocTitle  {...this.props} />
            <Slider {...settings} children={speakers} className="bloc-content">
            </Slider>
            <div className="clearfix"></div>
        </div>
        </DynamicBloc>
    }
}

@injectcontext({ router : React.PropTypes.object })
class BlocSpeakersItem extends React.Component<any, any> {
    constructor(props){
        super(props);
        this.openSpeaker = this.openSpeaker.bind(this);
    }

    openSpeaker(){
        var speaker = this.props.speaker;
        var router = this.context["router"] as any;
        router.push("/preview/" + this.props.event.id + "/speakers/" + speaker.id);
    }

    render() {
        var speaker = this.props.speaker;
        var speakerPicStyle = {} as any;
        if (speaker.photo) {
            speakerPicStyle.backgroundImage = 'url("' + speaker.photo + '")';
        }

        return <div className="speaker clickable" onClick={this.openSpeaker}>
            <div className="speaker-pic" style={speakerPicStyle}></div>
            <div className="speaker-name">
                { speaker.firstname + " " + speaker.lastname}
            </div>
        </div>
    }
}


