import * as React from 'react';
import {AppPage}  from '../../sharedcomponents/page';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {Toggle} from '../../sharedcomponents/toggle'
import {actions as preferencesActions } from '../../services/user.preferenceservice';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import './profile.less';

export interface ProfilePageProps {
    user: States.IAppUserIdentityState,
    event: States.ICurrentEventState,
    thematiques : States.IEventThematiquesState,
    preferences: States.IAppUserPreferencesState,
    preferencesActions: typeof preferencesActions,
    visualTheme : States.IEventVisualThemeState
}

class ProfilePageComponent extends React.Component<ProfilePageProps, any> {
    constructor(props) {
        super(props);
        this.state = {
            selectedpanel: "profile"
        }

        var tab = (props as any).location.query.tab;
        if (tab) {
            this.state.selectedpanel = tab;
        }
    }

    selectTab(arg, tabid) {
        arg.preventDefault();
        this.setState({ selectedpanel: tabid });
    }

    renderTabHeader(code, title) {
        return <div className="tabheader clickable" onClick={(arg) => this.selectTab(arg, code) }>{title}</div>
    }

    getCurrentContent() {
        if (this.state.selectedpanel == "profile") {
            return <UserDescription {...this.props} />
        }
        else if (this.state.selectedpanel == "networking") {
            return <SocialNetworking {...this.props} />
        }
        else if (this.state.selectedpanel == "notifs") {
            return <Notifications {...this.props} />
        }
    }

    render() {
        var content = this.getCurrentContent();

        return <AppPage className="profilepage" defaultPadding={false}>
            <header className="tabheaders">
                {this.renderTabHeader("profile", "Profile") }
                {this.renderTabHeader("networking", "Networking") }
                {this.renderTabHeader("notifs", "Notifications") }
            </header>
            <ReactCSSTransitionGroup component="section" className="tab-container"
                transitionName="fadetransition"
                transitionEnterTimeout={300}
                transitionLeave={false}>
                {content}
            </ReactCSSTransitionGroup>
        </AppPage>
    }
}

function mapStateToProps(state: States.IAppState) {
    return {
        preferences: state.user.preferences,
        thematiques : state.event.thematiques
    }
}

function mapDispatchToProps(dispatch) {
    return {
        preferencesActions: bindActionCreators(preferencesActions, dispatch)
    }
}

export var ProfilePage: new (any) => React.Component<any, any> = connect(mapStateToProps, mapDispatchToProps)(ProfilePageComponent as any) as any;


class UserDescription extends React.Component<ProfilePageProps, any> {
    render() {
        return <div className="panel userdescription">
            <div className="dynamiccontentblocwrapper">
                <div className="dynamiccontentbloc">
                    <h3>{ this.props.user.fullname }</h3>
                </div>
            </div>
        </div>
    }
}

class SocialNetworking extends React.Component<ProfilePageProps, any> {
    handleAllowChange(checked: boolean) {
        this.setSocialNetworkingPrefs({ acceptSocialNetworking: checked, hasChosenNetworkingOrNot: true });
    }

    setSocialNetworkingPrefs(delta) {
        var prefs = _.assign({}, this.props.preferences, {
            socialNetworking: _.assign({}, this.props.preferences.socialNetworking, delta)
        });
        this.props.preferencesActions.setPreferences(prefs);
        this.setState({});
    }

    render() {
        var details;
        if (this.props.preferences.socialNetworking.acceptSocialNetworking) {
            details = <SocialNetworkingDetails {...this.props} />
        } else {
            details = <div className="details message">
                Cette fonctionnalit vous permet de prendre contact avec
                tous les participants pour trouver ou proposer de l'aide. Vos informations personnelles 
                ne sont pas transmises aux personnes avec lesquelles vous rentrer en contact.
                Les changes se font intgralement  travers cette application,  moins que vous n'en dcidiez 
                autrement.
            </div>
        }
        return <div className="panel socialnetwork">
            <div className="dynamiccontentblocwrapper">
                <div className="dynamiccontentbloc">
                    <div className="field">
                        <Toggle
                            checked={this.props.preferences.socialNetworking.acceptSocialNetworking}
                            onChange={this.handleAllowChange.bind(this) } />
                        <label>Autoriser les interactions avec les autres participants, partenaires et speakers</label>
                    </div>
                    {details}
                </div>
            </div>
        </div>
    }
}

class SocialNetworkingDetails extends React.Component<ProfilePageProps, any> {
    render() {
        var themes;
        if (this.props.thematiques) {
            themes = this.props.thematiques.thematiques.map((t) => {
                return <SocialNetworkingTheme key={t.id} {...this.props} thematique={t} />
            });
        }
        return <div className="details">
            <div className="message">
                Selectionnez les thmatiques qui vous concernent.Les thmatiques vous permettent d'changer de facon
                communautaire ou de rechercher des personnes susceptibles de correspondre  vos attentes.
            </div>
            <div className="thematiques-list">
                {themes}
            </div>
        </div>
    }
}


interface SocialNetworkingThemeProps extends ProfilePageProps {
    thematique: Entities.IEventThematique;
}

class SocialNetworkingTheme extends React.Component<SocialNetworkingThemeProps, any> {
    constructor(props: SocialNetworkingThemeProps) {
        super(props);
        var networking = this.props.preferences.socialNetworking;
        networking.socialNetworkingThemes = networking.socialNetworkingThemes || [];

        this.state = {
            expanded: false
        }
    }

    toggleThematique(isSelected) {
        var networking = this.props.preferences.socialNetworking;
        var registered = networking.socialNetworkingThemes.filter((th) => th.thematiqueId == this.props.thematique.id)[0];

        if (isSelected) {
            if (!registered) {
                networking.socialNetworkingThemes.push({
                    thematiqueId: this.props.thematique.id,
                    searching: [],
                    proposing: []
                });

                var prefs = _.assign({}, this.props.preferences, {
                    socialNetworking: _.assign({}, this.props.preferences.socialNetworking, {
                        socialNetworkingThemes: networking.socialNetworkingThemes
                    })
                });
                this.props.preferencesActions.setPreferences(prefs);
            }
            this.setState({ expanded: true });
        } else {
            var idx = networking.socialNetworkingThemes.indexOf(registered);
            if (idx >= 0) {
                networking.socialNetworkingThemes.splice(idx, 1);
            }
            var prefs = _.assign({}, this.props.preferences, {
                socialNetworking: _.assign({}, this.props.preferences.socialNetworking, {
                    socialNetworkingThemes: networking.socialNetworkingThemes
                })
            });
            this.props.preferencesActions.setPreferences(prefs);
            this.setState({ expanded: false });
        }
    }

    render() {
        var networking = this.props.preferences.socialNetworking;
        var registered = networking.socialNetworkingThemes.filter((th) => th.thematiqueId == this.props.thematique.id)[0];
        var thematique = this.props.thematique;
        var userthematique = this.state.registered as Entities.IAppUserThematique;
        var content;
        if (this.state.expanded) {
            content = <SocialNetworkingThemeDetail {...this.props} />
        }

        var descriptionStyle = {
            //backgroundColor : this.props.event.theme.primaryColor,
            color: this.props.visualTheme.theme.primaryColor,
        }

        return <div className="thematique">
            <div className="description clickable" style={descriptionStyle} >
                <div className="title" onClick={() => this.setState({ expanded: !this.state.expanded }) }>
                    {thematique.title}
                </div>
                <Toggle
                    checked={ registered != null && registered != undefined }
                    onChange={this.toggleThematique.bind(this) } />
            </div>
            {content}
        </div>
    }
}

class SocialNetworkingThemeDetail extends React.Component<SocialNetworkingThemeProps, any> {
    apply(field: string, action: Entities.IEventThematiqueAction) {
        var networking = this.props.preferences.socialNetworking;
        var registered = networking.socialNetworkingThemes.filter((th) => th.thematiqueId == this.props.thematique.id)[0];
        if (!registered) {
            registered = {
                thematiqueId: this.props.thematique.id,
                searching: [],
                proposing: [],
            }
            networking.socialNetworkingThemes.push(registered);
        }

        var idx = registered[field].indexOf(action.id);
        if (idx >= 0) {
            registered[field].splice(idx, 1);
        } else {
            registered[field].push(action.id);
        }
        var prefs = _.assign({}, this.props.preferences, {
            socialNetworking: _.assign({}, this.props.preferences.socialNetworking, {
                socialNetworkingThemes: networking.socialNetworkingThemes
            })
        });
        this.props.preferencesActions.setPreferences(prefs);
        this.setState({});
    }

    toggleForSearching(action: Entities.IEventThematiqueAction) {
        this.apply("searching", action);
    }

    toggleForProposing(action) {
        this.apply("proposing", action);
    }

    render() {
        var networking = this.props.preferences.socialNetworking;
        var registered = networking.socialNetworkingThemes.filter((th) => th.thematiqueId == this.props.thematique.id)[0];

        var actionsForSearch, actionsToPropose;
        if (this.props.thematiques.thematiquesActions) {
            actionsForSearch = this.props.thematiques.thematiquesActions.map((act) => {
                var isSelected = registered && (registered.searching.indexOf(act.id) >= 0);
                return <div key={act.id}
                    className={ "action clickable" + (isSelected ? " selected" : "") }
                    onClick={(arg) => this.toggleForSearching(act) }>
                    {act.title}
                </div>
            });

            actionsToPropose = this.props.thematiques.thematiquesActions.map((act) => {
                var isSelected = registered && (registered.proposing.indexOf(act.id) >= 0);
                return <div key={act.id}
                    className={ "action clickable" + (isSelected ? " selected" : "") }
                    onClick={(arg) => this.toggleForProposing(act) }>
                    {act.title}
                </div>
            });
        }

        return <div className="thematique-content">
            <div className="actionbloc">
                <h4>Je cherche</h4>
                <div className="action-list">
                    {actionsForSearch}
                </div>
            </div>
            <div className="actionbloc">
                <h4>Je propose</h4>
                <div className="action-list">
                    {actionsToPropose}
                </div>
            </div>
        </div>
    }
}

class Notifications extends React.Component<ProfilePageProps, any> {
    render() {
        return <div className="panel socialnetwork">
            notifs
        </div>
    }
}


