export function userIdentityReducer(state = { login: null, fullname : null, message : null}, action = { type: '', payload: <any>null }) {
    switch (action.type) {
        case "USER_SIGNEDIDN": {
            return _.assign({}, {
                    login : action.payload.login, 
                    fullname : action.payload.fullname, 
                    message:action.payload.message 
                });
        }

        default:
            return state;
    }
}



