import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {connect} from 'react-redux';
import {AppPage, AppPageHeader} from '../../sharedcomponents/page';
import {entityFieldsCatalog} from '../../../data/fakedata';
import {AvailableFields} from './entityschema.availablefields';
import {EntityFields} from './entityschema.fields';
import {SideModal} from '../../sharedcomponents/popover/modal';
import {EntityEditPage} from '../../routes/entityedit/entityedit';

import './entityschema.less';

interface EntitySchemaPageProps {
    entities : States.IEventEntitiesState
}
class EntitySchemaPageComponent extends React.Component<EntitySchemaPageProps, any> {
    constructor(props){
        super(props);
        this.testSchema = this.testSchema.bind(this);
        this.hideSideModal = this.hideSideModal.bind(this);
        this.state = {
            fields : entityFieldsCatalog,
            showSideModal : false,
            schema : this.props.entities.schemas.sessions
        }
    }

    testSchema(){
        this.setState({ showSideModal:  true });
    }

    hideSideModal(){
        this.setState({ showSideModal:  false });
    }

    render(){
        return <AppPage className="entityschemapage" defaultPadding={ true }>
            <AppPageHeader i18n="entities.schemaeditor.title">
                <button onClick={ this.testSchema }>test</button>
            </AppPageHeader>
            <EntitySchemaSection {...this.props} />
            <SideModal show={this.state.showSideModal} onhide={ this.hideSideModal }>
                <EntityEditPage schema={ this.state.schema } />
            </SideModal>
        </AppPage>
    }
}

function mapStateToProps(state : States.IAppState) {
    return { 
        entities: state.event.entities
    }
}

export var EntitySchemaPage: new (any) => React.Component<any, any> = connect(mapStateToProps)(EntitySchemaPageComponent as any) as any;


export class EntitySchemaSection extends React.Component<EntitySchemaPageProps, any> {
    constructor(props){
        super(props);
        this.dropCanceled = this.dropCanceled.bind(this);
        this.state = {
            fields : entityFieldsCatalog,            
            schema : this.props.entities.schemas.sessions
        }
    }

    dropCanceled(){
        var fields = this.refs["fields"] as any;
        console.log("drop canceled");
        fields.decoratedComponentInstance.restoreFields();
    }

    render(){
        return <section>
                <AvailableFields fields={ this.state.fields } dropCanceled={this.dropCanceled} />
                <EntityFields ref="fields" schema={ this.state.schema } />
            </section>
    }
}



