import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {DynamicContent} from '../sharedcomponents/dynamiccontent/dynamiccontent';
import {Loader} from '../sharedcomponents/loader';
import {connect} from 'react-redux';
import {AppPage} from '../sharedcomponents/page';

export interface DynamicPageProps {
    user : States.IAppUserState;
    event : States.ICurrentEventState;
    visualTheme : States.IEventVisualThemeState;
    templates : States.IEventTemplatesState;
    template : string;
    context? : any;
    className? : string;
}

export class DynamicPageComponent extends React.Component<DynamicPageProps, any> {
    constructor(props:DynamicPageProps){
        super(props);

        this.state = {
            template : props.event ? props.templates.templates[this.props.template] : null
        }
    }

    componentWillReceiveProps(nextprops){
        if (nextprops.templates && nextprops.templates.templates){
            this.setState({
                template : nextprops.templates ? nextprops.templates.templates[this.props.template] : null
            })
        }
    }

    render(){        
        var content;

        if (this.props.event){
            content = <DynamicContent template={ this.state.template } visualTheme={this.props.visualTheme} context={this.props.context} />
        }else{
            content = <div className="loading"><Loader /></div>
        }

        return <AppPage className={ this.props.className || "" } withHeader={false} defaultPadding={false}>
            {content}
        </AppPage>
    }
}

function mapStateToProps(state: States.IAppState) {
    return { 
        user: state.user,
        event : state.event.current,
        templates : state.event.templates
    }
}

export var DynamicPage: new (any) => React.Component<DynamicPageProps, any> = connect(mapStateToProps)(DynamicPageComponent as any) as any;


