import * as _ from 'lodash';
import * as React from 'react';
import {AppLabel} from '../services/i18nservice';

import './card.less';

export interface CardProps {
    defaultPadding? : boolean;
    scrollable? : boolean;
    dark? : boolean;
    className? : string;
    onClick? : (event:MouseEvent)=>void;
}

export class Card extends React.Component<CardProps, any> {
    render(){
        var classes = ['card', this.props.className || ''];
        if (!(this.props.defaultPadding == false))
            classes.push("defaultPadding");
        if (this.props.dark)
            classes.push("dark");
            
        return <div className={ classes.join(" ") } onClick={this.props.onClick}>
            {this.props.children}
        </div>
    }
}

export interface CardHeaderProps {
    i18n? : string;    
    title? : string;
    inject? : any;
}

export class CardHeader extends React.Component<CardHeaderProps, any> {
    render(){        
        var title, actions;
        if (this.props.children){
            actions = <div className="actions">
                {this.props.children}
            </div>
        }

        if (this.props.i18n){
            title = <AppLabel component="h1" i18n={ this.props.i18n} inject={this.props.inject} />
        } else if (this.props.title){
            title = <h1>{ this.props.title }</h1>
        }

        return <header className="app-page-header">
            {title}
            {actions}
        </header>
    }
}


