import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {BlocTitle,DynamicBloc, DynamicContentBlocProps} from './common';

//import './bloc.pictureteaser.less';

export class BlocThematiqueTeaser extends React.Component<DynamicContentBlocProps, any> {
    render(){    
        var template = this.props.template;
        var thematique = this.props.context.thematique as Entities.IEventThematique;
        var header  = { 
            title : { "fr" : thematique.title}, 
            description : { "fr" : thematique.description } 
        } as PageBlocs.IBlocHeaderTemplate;
        
        return <DynamicBloc {...this.props}>
            <div className={"dynamic-bloc-thematiqueteaser " + this.props.className }>
                <BlocTitle  {...this.props} header={header} />                
            </div>
        </DynamicBloc>
    }
}


