import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {connect} from 'react-redux';
import {AppPage} from '../../sharedcomponents/page';
import {EntityForm} from '../../sharedcomponents/entityform/entityform';

interface EntityEditPageProps {
    entities : States.IEventEntitiesState;
    schema? : any;
}

export class EntityEditPageComponent extends React.Component<EntityEditPageProps, any> {
    constructor(props){
        super(props);
        this.state = {
            isDirty : false,
            isValid : false,
            schema : this.props.schema || this.props.entities.schemas.sessions
        }
    }

    canClose(){
        if (this.state.isDirty){
            return confirm("save changes ?");
        }

        return true;
    }

    render(){
        return <AppPage className="homepage" defaultPadding={ true }>
            <header>
                <h2>test</h2>
            </header>
            <section>
                <EntityForm ref="dataform" 
                    schema={this.state.schema} 
                    currentLanguage={"fr"} 
                    entity={{}} 
                    onDirtyChanged={(dirty) => this.setState({ isDirty : dirty })}
                    onValidityChanged={(valid) => this.setState({ isValid : valid })} />
            </section>
            <footer>
                <button disabled={!this.state.isDirty}>Save</button>
            </footer>
        </AppPage>
    }
}

function mapStateToProps(state:States.IAppState) {
    return { 
        entities: state.event.entities 
    }
}

export var EntityEditPage: new (any) => React.Component<any, any> = connect(mapStateToProps)(EntityEditPageComponent as any) as any;


