

var INITIAL_STATE = {
    states : []
} as States.IAppVisualState;

export function visualStateReducer(state = INITIAL_STATE, action = { type: '', payload: <any>null }) {
    switch (action.type) {
        case "VISUALSTATE_CURRENT": {
            return _.assign({}, state, { states : action.payload.states });
        }

        default:
            return state;
    }
}


export var actions = {
    currentStates(states : string[]){
        return (dispatch, state) => {
            dispatch({ type : "VISUALSTATE_CURRENT",  payload : { states : states }});            
        }
    }
}


