import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {DynamicContentBloc} from './dynamiccontentbloc';
import {connect} from 'react-redux';
import './dynamiccontent.less';

export interface DynamicContentProps {
    template : PageBlocs.IContentTemplate;
    user? : States.IAppUserState;
    event? : States.ICurrentEventState;
    visualTheme : States.IEventVisualThemeState;
    i18nService? : States.i18nState;
    context : any;    
}

export interface DynamicContentState {    
}

class DynamicContentComponent extends React.Component<DynamicContentProps, DynamicContentState> {
    constructor(props){
        super(props);
        this.state = {
            theme : null
        }
    }

    render(){
        var blocs;
        if (this.props.template){
            blocs = this.props.template.blocs.map((b) => {
                var theme  = b.theme;
                if (b.themePreset){
                    theme = this.props.visualTheme.theme.blocThemes.filter((t) => t.id == b.themePreset)[0];
                } 
                if (!theme){
                    theme = this.props.visualTheme.theme.defaultTheme;
                }

                return <DynamicContentBloc key={b.id} 
                    template={b} 
                    theme={theme}
                    context={this.props.context}
                    user={ this.props.user} 
                    event={this.props.event} 
                    
                    visualTheme={ this.props.visualTheme } 
                    i18nService={ this.props.i18nService } />
            })
        }

        return <div className="dynamiccontent">
            { blocs }            
        </div>
    }
}

function mapStateToProps(state) {
    return { 
        user: state.user,
        event: state.event.current,
        i18nService : state.i18n
    }
}

export var DynamicContent: new (any) => React.Component<DynamicContentProps, any> = connect(mapStateToProps)(DynamicContentComponent as any) as any;


