import * as Promise from 'bluebird';
import * as $ from 'jquery';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {connect} from 'react-redux';
import {injectcontext} from '../utils/decorators';

const INITIAL_STATE = {
    defaultlng: "en",
    supported: ["en", "fr"],
    currentLanguage: null,
    currentLanguageCode: "en",
    isReady: false
} as States.i18nState;

export function i18nReducer(state = INITIAL_STATE, action = { type: '', payload: null }) {
    switch (action.type) {
        case "I18N_SET": {
            return _.assign({}, state, {
                currentLanguageCode: action.payload.code,
                currentLanguage: action.payload.translations,
                isReady: true
            });
        }

        case "I18N_SET": {
            return _.assign({}, state, { isReady : false });
        }

        default:
            return state;
    }
}

export var actions = {
    loadLanguage(languageCode) {
        return (dispatch) => {
            console.log("loading i18n for " + languageCode);
            dispatch({ type: 'I18N_LOADING', payload: { code: languageCode } });

            $.ajax({
                url: 'trads.json'
            }).then((res) => {
                if (res) {
                    console.log("loaded i18n for " + languageCode);
                    dispatch({
                        type: 'I18N_SET',
                        payload: {
                            code: languageCode,
                            translations: res,
                        }
                    });
                }
            }, (err) => {
                dispatch({ type: 'I18N_LOADERROR', payload: { code: languageCode, error: err } });
                console.error(err);
            });
        }
    }
}

export function translate(service : States.i18nState, key: string, inject? : any) {
    if (service && key && service.currentLanguage){
        var lng = service.currentLanguage[service.currentLanguageCode];
        var txt = lng[key] || key;
        if (inject){
            var keys = Object.keys(inject);
            keys.forEach((k) => {
                txt = txt.replace("{" + k + "}", inject[k]);
            });
        }
        return txt;
    }
    return key;
}

export interface AppLabelProps {
    i18n: string;
    htmlFor? : string;
    i18nService?: any;
    className?: string;
    component?: string;
    inject? : any;
}

class AppLabelComponent extends React.Component<AppLabelProps, any> {
    render() {
        var txt = "";
        if (this.props.i18nService && this.props.i18nService.isReady) {
            txt = translate(this.props.i18nService, this.props.i18n, this.props.inject); //            
        }

        return React.createElement(this.props.component || 'span', { 
            className: this.props.className, 
            htmlFor : this.props.htmlFor 
        }, txt);
    }
}

function mapStateToProps(state) {
    return { i18nService: state.i18n }
}

export var AppLabel: new (AppLabelProps) => React.Component<AppLabelProps, any> = connect(mapStateToProps)(AppLabelComponent as any) as any;


