import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {AppLabel} from '../../services/i18nservice';

export interface CommonEditorPartsProps{
    field : IEntityFieldSchema;
    onChange : (field) => void;
}

export class CommonEditorParts extends React.Component<CommonEditorPartsProps, any> {
    render(){        
        return <div className="commoneditor">
            <h4 className="fieldtype">
                { this.props.field.type }
            </h4>
            <div className="field">
               <AppLabel component="label" i18n="entity.fields.key" 
                    htmlFor="fieldkey" 
                     />
                <input type="text" className="stretch" name="fieldkey"
                    value={ this.props.field.key } 
                    onChange={ (arg) => { this.props.field.key = (arg.target as HTMLInputElement).value; this.props.onChange(this.props.field) }} />
            </div>
            <CommonEditorLabels field={ this.props.field } onChange={ this.props.onChange} />
            <div className="field">
                <input type="checkbox" name="isMandatory" 
                    value={this.props.field.isMandatory} 
                    onChange={ (arg) => this.props.field.isMandatory = (arg.target as HTMLInputElement).checked } />
                <AppLabel component="label" className="forcheckbox" i18n="entity.fields.isMandatory" 
                    htmlFor="isMandatory" 
                     />
            </div>
            <div className="field">
                <input type="checkbox" name="isLocalizable"
                    value={this.props.field.isLocalizable} 
                    onChange={ (arg) => this.props.field.isLocalizable = (arg.target as HTMLInputElement).checked } />
                <AppLabel component="label" className="forcheckbox" i18n="entity.fields.isLocalizable" htmlFor="isLocalizable" />
            </div>
        </div>
    }
}

export class CommonEditorLabels extends React.Component<CommonEditorPartsProps, any> {
    render(){  
        var labels;
        var lngKeys = Object.keys(this.props.field.labels);
        labels = lngKeys.map((k) => {
            return <CommonEditorLabel key={k} labelkey={k} item={ this.props.field.labels} onChange={() => this.props.onChange(this.props.field) } />
        }); 
        return <div className="commoneditorlabels">
            <AppLabel component="h4" i18n="entity.labels" />
            <div className="labels">
                {labels}
            </div>
        </div>
    }
}

export interface CommonEditorLabelProps{
    item : any;
    labelkey : string;
    onChange : () => void;
}

export class CommonEditorLabel extends React.Component<CommonEditorLabelProps, any> {
    render(){     
        return <div className="field">
            <label>{ this.props.labelkey }</label>
            <input type="text" className="stretch" name={this.props.labelkey}
            	value={this.props.item[this.props.labelkey]} 
                onChange={ (arg) => { this.props.item[this.props.labelkey] = (arg.target as HTMLInputElement).value; this.props.onChange(); }} />
        </div>
    }
}


