import * as React from 'react';
import {AppPage}  from '../../sharedcomponents/page';
import {DynamicPage} from '../dynamicpage';
import {connect} from 'react-redux';

import './sessiondetail.less';

export interface SessionDetailPageProps{
    user? : States.IAppUserState;
    event? : States.ICurrentEventState;
    visualTheme? : States.IEventVisualThemeState;
    templates : States.IEventTemplatesState,
    sessions : States.IEventSessionsState,
    entities : States.IEventEntitiesState
}

class SessionDetailPageComponent extends React.Component<SessionDetailPageProps, any> {
    render(){
        var sessionid = (this.props as any).params.sessionid;
        var session = this.props.sessions.sessions.filter((t) => t.id == sessionid)[0];
        var context = {
            session : session,
            entity : session,
            entitySchema : this.props.entities.schemas.session
        }
        return <DynamicPage template="session" context={context} user={this.props.user} event={this.props.event} visualTheme={ this.props.visualTheme } templates={this.props.templates} />
    }
}

function mapStateToProps(state: States.IAppState) {
    return { 
        user: state.user,
        event : state.event.current,
        templates : state.event.templates,
        sessions : state.event.sessions,
        entities : state.event.entities,
    }
}

export var SessionDetailPage: new (any) => React.Component<SessionDetailPageProps, any> = connect(mapStateToProps)(SessionDetailPageComponent as any) as any;


