import * as _ from 'lodash';
import * as React from 'react';
import {PropTypes} from 'react';
import * as ReactDOM from 'react-dom';
import './loader.less';

export class Loader extends React.Component<any, any> {
    render(){
        return <div className={"loaderindicator " + (this.props.className || "") + (this.props.theme ? " theme-" + this.props.theme: "")}>
            { this.props.message || 'Chargement'} <i className="spinner"></i>
        </div>
    }
}


