import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {connect} from 'react-redux';
import {injectcontext} from '../../utils/decorators';
import {DynamicBloc, BlocTitle, IDynamicContentItemsBlocProperties,DynamicContentBlocProps} from './common';
import * as Slider from 'react-slick';

import './bloc.exhibitors.less';

interface BlocExhibitorsProps extends DynamicContentBlocProps {
    event : States.ICurrentEventState,
    exhibitors : States.IEventExhibitorsState,
    visualState : States.IAppVisualState
}

export class BlocExhibitorsComponent extends React.Component<BlocExhibitorsProps, any> {
    render(){
        var properties = (this.props.template.properties || {}) as IDynamicContentItemsBlocProperties;
        if (properties.itemsDisplayMode == "carousel"){
            return <BlocExhibitorsCarousel {...this.props} />
        } else if (properties.itemsDisplayMode == "list"){
            return <BlocSpeakersList {...this.props} />
        } else {
            return <BlocSpeakersList {...this.props} />
        }
    }
}

function mapStateToProps(state : States.IAppState) {
    return { 
        exhibitors: state.event.exhibitors,
        visualState : state.visualstate
    }
}

export var BlocExhibitors: new (any) => React.Component<any, any> = connect(mapStateToProps)(BlocExhibitorsComponent as any) as any;

class BlocSpeakersList extends React.Component<BlocExhibitorsProps, any> {
    render() {
        var exhibitors;
        if (this.props.event && this.props.exhibitors) {
            var list = this.props.exhibitors.exhibitors;
            if (this.props.template.properties.maxItems) {
                list = list.slice(0, this.props.template.properties.maxItems);
            }

            exhibitors = list.map((s) => {
                var speaker = s;
                return <BlocExhibitorsItem exhibitor={s} {...this.props} key={speaker.id} />;
            })
        }

        return <DynamicBloc {...this.props}><div className={"bloc-exhibitors list" + this.props.className }>
            <BlocTitle  {...this.props} />
            <div className="bloc-exhibitors-items bloc-content">
                {exhibitors}
            </div>
        </div>
        </DynamicBloc>
    }
}

class BlocExhibitorsCarousel extends React.Component<BlocExhibitorsProps, any> {
    render() {
        var settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: Math.min(this.props.template.properties.maxItems, 5),
            slidesToScroll: Math.min(this.props.template.properties.maxItems, 5)
        };

        if (!(this.props.visualState.states.indexOf("S") >= 0)){
            settings.slidesToShow = 2;
            settings.slidesToScroll = 2;
        } else if (!(this.props.visualState.states.indexOf("M") >= 0)){
            settings.slidesToShow = 3;
            settings.slidesToScroll = 3;
        }else if (!(this.props.visualState.states.indexOf("L") >= 0)){
            settings.slidesToShow = 4;
            settings.slidesToScroll = 4;
        }

        var exhibitors;
        if (this.props.event && this.props.exhibitors) {
            var list = this.props.exhibitors.exhibitors;
            if (this.props.template.properties.maxItems) {
                list = list.slice(0, this.props.template.properties.maxItems);
            }

            exhibitors = list.map((s) => {
                var exhibitor = s;
                return <div className="exhibitorslide" key={exhibitor.id}>
                    <BlocExhibitorsItem exhibitor={s} { ...this.props } />
                </div>
            })
        }

        return <DynamicBloc {...this.props}><div className={"bloc-exhibitors carousel " + this.props.className }>
            <BlocTitle {...this.props} />
            <Slider {...settings} children={exhibitors} className="bloc-content">
            </Slider>
            <div className="clearfix"></div>
        </div>
        </DynamicBloc>
    }
}

interface BlocExhibitorsItemProps extends BlocExhibitorsProps {
    exhibitor : Entities.IExhibitor;
}

@injectcontext({ router : React.PropTypes.object })
class BlocExhibitorsItem extends React.Component<BlocExhibitorsItemProps, any> {
    constructor(props){
        super(props);
        this.openExhibitor = this.openExhibitor.bind(this);
    }

    openExhibitor(){
        var exhibitor = this.props.exhibitor;
        var router = this.context["router"] as any;
        router.push("/preview/" + this.props.event.id + "/exhibitors/" + exhibitor.id);
    }

    render() {
        var exhibitor = this.props.exhibitor;
        if (!exhibitor.logo){
            return <div className="exhibitor nologo clickable" onClick={this.openExhibitor}>
                <div className="exhibitor-name">{ exhibitor.name }</div>            
            </div>    
        }

        return <div className="exhibitor clickable" onClick={this.openExhibitor}>
            <img src={ exhibitor.logo } />            
        </div>
    }
}


