import * as React from 'react';
import {Panel as ColorPickerPanel} from 'rc-color-picker';
import {Popover} from './popover/popover';
import 'rc-color-picker/assets/index.css';
import './colorpicker.less';

export interface ColorPickerProps {
    color: string;
    onChange? : (color) => void;
}

export class ColorPicker extends React.Component<ColorPickerProps, any> {
    constructor(props: ColorPickerProps){
        super(props);
        this.showModal = this.showModal.bind(this);
        this.hideModal = this.hideModal.bind(this);
        this.saveValue = this.saveValue.bind(this);
        this.state = {
            color : props.color || '#FFF',
            showModal : false,
            modalParent : null
        }
    }

    showModal(arg){
        this.setState({ showModal: true, modalParent: arg.target });
    }

    hideModal(){
        this.setState({ showModal: false });
    }

    saveValue(color){        
        if (this.props.onChange)
            this.props.onChange(color);

        this.setState({ color: color, showModal: false });
    }

    render() {        
        var pickerstyle = { backgroundColor : this.state.color };
        return <div className="colorpicker">
            <div className="bloc" style={pickerstyle} onClick={this.showModal}></div>
            <Popover className="colorpickermodal" 
                show={ this.state.showModal }
                parent={ this.state.modalParent }
                onhide={ this.hideModal}>
                <ColorPickerModal color={this.props.color} onsave={ this.saveValue } onhide={ this.hideModal } />
            </Popover>
        </div>
    }
}

export class ColorPickerModal extends React.Component<any, any> {
    constructor(props){
        super(props);
        this.state ={
            color : this.props.color
        };
    }

    render(){
        //<SketchPicker color={this.state.color} />
        return <div className="colorpickermodalcontent">
            <ColorPickerPanel color={this.state.color} onChange={(arg) => { this.setState({ color : arg.color }); }} />
            <div className="actions">
                <button onClick={(arg) => { this.props.onsave(this.state.color)}}>Ok</button>
                <button onClick={(arg) => { this.props.onhide()}}>Annuler</button>
            </div>
        </div>
    }
}



