import * as Promise from 'bluebird';
import { eventTheme } from '../../fakedata/visualtheme';
import { eventThematiques, eventThematiquesActions } from '../../fakedata/thematiques';
import { sessionsList } from '../../fakedata/sessions';
import { speakersList } from '../../fakedata/speakers';
import { sessionSchema } from '../../fakedata/sessionSchema';
import { speakerSchema } from '../../fakedata/speakerSchema';
import { homePageTemplate } from '../../fakedata/templates.home';
import {push} from 'react-router-redux';
import {combineReducers} from 'redux';
import {userPreferencesReducer} from './user.preferenceservice';
import {userStatusReducer} from './user.statusservice';
import {userEventsReducer} from './user.eventsservice';
import {userIdentityReducer} from './user.identityservice';

var INITIAL_STATE = {
    status : {
        loading : true,
        message : "user.check",
    },
    identity : {
        login : null,
        fullname: null,
        permissions: [],
    },
    availableevents : [],
    event: null,
    preferences : { socialNetworking : {}, notifications: {}}
} as States.IAppUserState;

export var userReducer = combineReducers({
    status : userStatusReducer,
    identity : userIdentityReducer,
    availableevents : userEventsReducer,
    preferences : userPreferencesReducer
});

export var actions = {
    checkUser() {
        return (dispatch) => {
            dispatch({ type : "USER_CHECK",  payload : { message : "user.loading" }});
            Promise.try(() => {
                return Promise.delay(200);
            }).then(() => {
                dispatch({type : "USER_SIGNEDIDN", payload : {
                    login : "johndoe@gmail.com",
                    fullname : "John Doe",
                    message: "user.loading.events",
                    permissions: [],
                }});
                return Promise.delay(200);
            }).then(() => {
                dispatch({ type : "USER_EVENTS",payload : {
                    events : <IAppUserEventShort[]>[
                        {
                            name : "My Event",
                            id : "abcd",                            
                        },
                        {
                            name : "My other event",
                            id : "efgh"
                        },
                    ]
                }});
            });
        }
    }
}



