import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {Link} from 'react-router';
import { injectcontext } from '../../utils/decorators';

export interface IDynamicContentItemsBlocProperties {
    itemsDisplayMode : 'carousel' | 'list';
    maxItems : number;
}

export interface BlocTitleProps extends DynamicContentBlocProps{ 
    header? : PageBlocs.IBlocHeaderTemplate;
}

export interface DynamicContentBlocProps{
    template : PageBlocs.IBlocTemplateBase;
    user? : States.IAppUserState;
    event? : States.ICurrentEventState;
    visualTheme : States.IEventVisualThemeState;   
    theme : VisualTheme.IBlocTheme; 
    i18nService? : States.i18nState;
    className? : string;
    bgPicture? : string;  
    context : any;  
}

export class DynamicBloc extends React.Component<DynamicContentBlocProps, any> {
    render(){
        var baseStyle = {} as any;
        var theme = this.props.theme;
        if (theme.textColor) 
            baseStyle.color = theme.textColor;
        if (theme.bgColor) 
            baseStyle.backgroundColor = theme.bgColor;

        var bgPicture = this.props.bgPicture || this.props.template.bgPicture;
        if (bgPicture) { 
            baseStyle.backgroundSize = 'cover';
            baseStyle.backgroundPosition = 'center';
            baseStyle.backgroundImage = 'url("' + bgPicture + '")'
        }
        var align = this.props.template.layout || "center";

        return <div className={ "dynamiccontentblocwrapper bloc-wrapper-" + this.props.template.type  + " align-" + align } style={baseStyle}>
            {this.props.children}
            <div className="clearfix"></div>
        </div>
    }
}

@injectcontext({ blocTheme : React.PropTypes.object })
export class BlocTitle extends React.Component<BlocTitleProps, any> {
    render(){       
        var pretitle, title, subtitle, description, actions;
        var language = "fr";
        var header = this.props.header || this.props.template.header;
        var theme = this.context["blocTheme"] as VisualTheme.IBlocTheme;
        
        if (header.preTitle){
            var titleStyle = {} as any;
            if (theme && theme.preTitleColor){
                titleStyle.color = theme.preTitleColor;
            }
            pretitle = <h3 className="pretitle" style={titleStyle}>{header.preTitle[language]}</h3>            
        } 

        if (header.title){
            var titleStyle = {} as any;
            if (theme && theme.titleColor){
                titleStyle.color = theme.titleColor;
            }
            title = <h1 className="bloctitle" style={titleStyle}>{header.title[language]}</h1>            
        } 

        // if (header.subtitle){
        //     var subtitleStyle = {} as any;
        //     if (this.props.schema.subtitlecolor){
        //         subtitleStyle.color = this.props.schema.subtitlecolor
        //     }
        //     subtitle = <h3 className="subtitle" style={subtitleStyle}>{this.props.schema.subtitle[language]}</h3>            
        // } 

        if (header.description){
            description = <div className="description">{header.description[language]}</div>            
        } 

        if (this.props.template.actions && this.props.template.actions.length){
            actions = <BlocActions {...this.props} />
        }

        return <header className={ "bloc-header" }>
           {pretitle}
           {title}
           {subtitle}
           {description}
           {actions}
        </header>
    }
}

export class BlocActions extends React.Component<DynamicContentBlocProps, any> {
    render(){      
        var actions; 
        if (this.props.template.actions){
            actions = this.props.template.actions.map((a) => {
                return <Link role="button" key="a.link" to={ getTargetPage(a.link, this.props.event, this.props.context )}>{ a.title["fr"]}</Link>
            });
        }

        return <div className="bloc-actions">
        {actions}
        </div>
    }
}

export function getTargetPage(link : string, event : States.ICurrentEventState, context : any):string{
    if (link == "allsessions")
        return "/preview/" + event.id + "/sessions";
    if (link == "allspeakers")
        return "/preview/" + event.id + "/speakers";
    if (link == "allexhibitors")
        return "/preview/" + event.id + "/exhibitors";

    return "/preview/" + event.id;
}

export class BlocLayout extends React.Component<BlocTitleProps, any> {
    render(){     
        return <div className="bloclayout">
            <BlocTitle {...this.props} />
        </div>
    }
}


