import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {CommonEditorParts} from './editor.common';

export class TextEditor extends React.Component<any, any> {
    render(){
        return <div className={ "fieldeditor " + this.props.className }>
            <CommonEditorParts field={this.props.field} onChange={ this.props.onChange } />
        </div>
    }
}


