import * as React from 'react';
import * as ReactDOM from 'react-dom';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import {Loader} from '../../sharedcomponents/loader';
import {AppPage} from '../../sharedcomponents/page';
import {Card} from '../../sharedcomponents/card';
import {AppLabel} from '../../services/i18nservice';
import {actions as userActions } from '../../services/userservice';
import { Router, withRouter } from 'react-router';
import {injectcontext} from '../../utils/decorators';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';

import './loginpage.less';

export interface LoginPageProps {
    location: {
        state: {
            nextPathname: string;
        }
    },
    router: any;
    user: States.IAppUserIdentityState,
    userEvents: IAppUserEventShort[],
    userStatus: States.IAppUserStatusState
}


export interface LoginPageState {
    login?: string;
    password?: string;
    message?: string;
}

@injectcontext({ router: React.PropTypes.object.isRequired })
class LoginPageComponent extends React.Component<LoginPageProps, LoginPageState> {
    pendingLoginRequest: any;

    constructor(props: LoginPageProps) {
        super(props);
        this.state = {
            login: "",
            password: "",
            message: null
        }
    }

    login() {
        const router = this.context["router"] as any;
        const { location } = this.props;
        if (location.state && location.state.nextPathname) {
            router.replace(location.state.nextPathname)
        } else {
            router.replace('/')
        }
    }

    render() {
        if (this.props.userEvents && this.props.userEvents.length) {
            if (!this.pendingLoginRequest) {
                this.pendingLoginRequest = setTimeout(() => {
                    this.login();
                }, 200);
            }
        }

        return <AppPage className="loginpage" defaultPadding={false} withHeader={false}>
            <Card className="loginform" defaultPadding={false}>
                <header>
                    <img src="assets/logo-lg.png" />
                </header>
                <section>
                    <AppLabel component="h3" i18n="loginpage.title" />
                    <div className="message">
                        { this.props.userStatus.message }
                    </div>
                </section>
                <footer>
                    <button onClick={ this.login.bind(this) }><AppLabel i18n="loginpage.loginbtn" /></button>
                </footer>
            </Card>
        </AppPage>
    }
}

function mapStateToProps(state: States.IAppState) {
    return {
        userStatus: state.user.status,
        userEvents: state.user.availableevents
    }
}

function mapDispatchToProps(dispatch) {
    return {
        userActions: bindActionCreators(userActions, dispatch)
    }
}

export var LoginPage: new (any) => React.Component<any, any> = connect(mapStateToProps, mapDispatchToProps)(LoginPageComponent as any) as any;



