import * as React from 'react';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {AppPage}  from '../../sharedcomponents/page';
import {utils} from '../../services/event.participantservice';
import {injectcontext} from '../../utils/decorators';
import './chat.less';

export interface ChatPageProps{
    participants : States.IParticipantsState;
    event: States.ICurrentEventState;
    visualTheme : States.IEventVisualThemeState;
}

class ChatPageComponent extends React.Component<ChatPageProps, any> {
    constructor(props){
        super(props);
        var contactid = (props as any).params.contactid;
        this.state = {
            contact : utils.byId(this.props.participants.allparticipants, contactid)
        }
    }

    render(){
        return <AppPage className="chatpage">
            <div className="chatpagecontent">
                <ChatContactsPanel {...this.props} contact={ this.state.contact } />
                <ChatMessagesPanel {...this.props} contact={ this.state.contact } />
            </div>
        </AppPage>
    }
}

function mapStateToProps(state: States.IAppState) {
    return {
        participants: state.event.participants
    }
}

export var ChatPage: new (any) => React.Component<any, any> = connect(mapStateToProps)(ChatPageComponent as any) as any;

export interface ChatContactsProps {
    participants : States.IParticipantsState;
    event: States.ICurrentEventState;
    visualTheme : States.IEventVisualThemeState;
    contact: Entities.IParticipant;
}

export class ChatContactsPanel extends React.Component<ChatContactsProps, any> {
    render(){
        var contactsList = this.props.participants.allparticipants.slice(0, 8); 
        var contacts = contactsList.map((p) => {
            return <ChatContactItem key={p.id} {...this.props} contact={p}/>
        });

        return <div className="chat-contactspanel">
            <h2>Mes contacts</h2>
            <div className="contact-items">
                {contacts}
            </div>
        </div>
    }
}

interface ChatContactItemProps extends ChatContactsProps{
    contact : Entities.IParticipant;
    onClick? : (contact : Entities.IParticipant) => void;
}

@injectcontext({ router : React.PropTypes.object })
export class ChatContactItem extends React.Component<ChatContactItemProps, any> {
    contactClicked(arg){
        var router = this.context["router"] as any;
        router.replace("/preview/" + this.props.event.id + "/chat/" + this.props.contact.id);
    }

    render(){
        var contact = this.props.contact;
        var contactPicStyle = {} as any;
        if (contact && contact.photo) {
            contactPicStyle.backgroundImage = 'url("' + contact.photo + '")';
        }

        return <div className="contact clickable" onClick={this.contactClicked.bind(this)}>
                <div className="contact-pic" style={contactPicStyle}></div>
                <div className="contact-name">
                    { contact.firstname + " " + contact.lastname}
                </div>
            </div>
    }
}

export class ChatMessagesPanel extends React.Component<ChatContactsProps, any> {
    render(){
        if (this.props.contact){
            return <div className="chat-messagespanel">
                <div className="chat-messagespanel-content">
                    <ChatMessagesTarget {...this.props} />
                    <ChatMessagesItems {...this.props} />
                    <ChatMessagesInput {...this.props} />
                </div>
            </div>
        }
        return <div className="chat-messagespanel">
        </div>
    }
}

export class ChatMessagesTarget extends React.Component<ChatContactsProps, any> {
    render(){
        var target;
        if (this.props.contact){
             return <div className="chat-messagestarget">
                <div className="chatwith">Messages avec </div>
                <ChatContactItem {...this.props} contact={this.props.contact} />
            </div>
        }

        return <div className="chat-messagestarget"></div>
    }
}

export class ChatMessagesItems extends React.Component<ChatContactsProps, any> {
    render(){
        return <div className="chat-messages"></div>
    }
}

export class ChatMessagesInput extends React.Component<ChatContactsProps, any> {
    render(){
        var content = <ChatMessageInputMessage {...this.props} />;
        return <div className="chat-messagesinput">
            <div className="chat-messagesinput-types">
                <a className="selected">Envoyer un message</a>
                <a>Proposer un rdv</a>
                <a>Demander ses coordonnes</a>
            </div>
            <div className="chat-messagesinput-content">
                {content}
            </div>
        </div>
    }
}

export class ChatMessageInputMessage extends React.Component<ChatContactsProps, any> {
    render(){
        return <div className="chat-messagesinput-message">
            <textarea className="txtsendmessage"></textarea>
            <div className="actions">
                <button>Envoyer</button>
            </div>            
        </div>
    }
}


