import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import {Loader} from '../../sharedcomponents/loader';
import {AppPage} from '../../sharedcomponents/page';
import {Card} from '../../sharedcomponents/card';
import {Modal, SideModal} from '../../sharedcomponents/popover/modal';
import {AppLabel} from '../../services/i18nservice';
import {actions as userActions} from '../../services/userservice';
import {actions as eventActions} from '../../services/eventservice';
import {EntityEditPage} from '../../routes/entityedit/entityedit';
import {injectcontext} from '../../utils/decorators';
import { Link, Router } from 'react-router';

interface MyEventsPageProps {
    user? : States.IAppUserIdentityState;
    userEvents : IAppUserEventShort[];
    userActions : typeof userActions;
    eventActions : typeof eventActions;
}

export class MyEventsPageComponent extends React.Component<MyEventsPageProps, any> {
    constructor(props){
        super(props);
    }

    selectEvent(event: IAppUserEventShort){
        this.props.eventActions.currentEvent(event, true);        
    }

    render(){
        var events;
        if (this.props.userEvents){
            events = this.props.userEvents.map((e) => {
                return <Card key={e.id} onClick={ () => this.selectEvent(e) }>
                    {e.name}
                </Card>
            })
        }

        return <AppPage className="myeventspage" withHeader={true} defaultPadding={true}>
            <header>
                <AppLabel component="h1" i18n="myevents.title" />
            </header>
            <section>
                <div className="card-grid">
                   {events}
                </div>
            </section>
        </AppPage>
    }
}


function mapStateToProps(state : States.IAppState) {
    return { 
        userEvents: state.user.availableevents,
        event : state.event 
    }
}

function mapDispatchToProps(dispatch) {
  return {
    userActions: bindActionCreators(userActions, dispatch),
    eventActions: bindActionCreators(eventActions, dispatch)
  }
}

export var MyEventsPage: new (MyEventsPageProps) => React.Component<MyEventsPageProps, any> = connect(mapStateToProps, mapDispatchToProps)(MyEventsPageComponent as any) as any;



