import * as React from 'react';
import './burgermenu.less';

export interface BurgerMenuProps {
    collapsed: boolean;
    style? : any;
    onClick: (arg) => void;
}

export class BurgerMenu extends React.Component<BurgerMenuProps, any> {
    menuClicked(arg) {
        this.props.onClick(arg);
    }

    render() {
        if (!this.props.collapsed) {
            document.body.classList.add("burger-on");
        } else {
            document.body.classList.remove("burger-on");
        }
        return <div className={ 'burger-menu' + (!this.props.collapsed ? ' expanded' : '') } onClick={ this.menuClicked.bind(this) } style={ this.props.style }>
            <div className="burger">
                <div className="split1"></div>
                <div className="split2"></div>
                <div className="split3"></div>
            </div>
        </div>
    }
}


