import { sessionSchema } from '../../fakedata/sessionSchema';
import { speakerSchema } from '../../fakedata/speakerSchema';

var INITIAL_STATE = <States.IEventEntitiesState>{
    loading : true,
    schemas : {}    
};

export function eventEntitiesReducer(state = INITIAL_STATE, action = { type: '', payload: <any>null }) {
    switch (action.type) {
        case "EVENT_CURRENT": {
            return _.assign({}, state, {
                loading : false, 
                schemas : {
                    sessions : sessionSchema,
                    speakers : speakerSchema
                } 
            });
        }

        default:
            return state;
    }
}

export var utils = {

}


