import { speakersList } from '../../fakedata/speakers';

var INITIAL_STATE =<States.IEventSpeakersState> {
    loading : true,
    speakers : []    
};

export function eventSpeakersReducer(state = INITIAL_STATE, action = { type: '', payload: <any>null }) {
    switch (action.type) {
        case "EVENT_CURRENT": {
            return _.assign({}, state, { 
                loading : false,
                speakers : speakersList 
            });
        }

        default:
            return state;
    }
}

export var utils = {

}


