import * as React from 'react';
import * as ReactDOM from 'react-dom';

import {DynamicContentBlocProps} from './common';

import {BlocNotFound} from './bloc.notfound';
import {BlocComingSessions} from './bloc.comingsessions';
import {BlocExhibitors} from './bloc.exhibitors';
import {BlocNews} from './bloc.news';
import {BlocPictureTeaser} from './bloc.pictureteaser';
import {BlocPracticalInfos} from './bloc.practicalinfos';
import {BlocSpeakers} from './bloc.speakers';
import {BlocSocialNetworking} from './bloc.socialnetworking';
import {BlocThematiqueTeaser} from './bloc.thematiqueteaser';
import {BlocThematiqueWall} from './bloc.thematiquewall';
import {BlocEntityDetail} from './bloc.entitydetail';
import {BlocEntityActions} from './bloc.entityactions';

var catalog = {
    "comingsessions" : BlocComingSessions,
    "exhibitors" : BlocExhibitors,
    "news" : BlocNews,
    "pictureteaser" : BlocPictureTeaser,
    "practicalinfos" : BlocPracticalInfos,
    "speakers" : BlocSpeakers,
    "socialnetworking" : BlocSocialNetworking,
    "thematiqueteaser" : BlocThematiqueTeaser,
    "thematiquewall" : BlocThematiqueWall,
    "entitydetail" : BlocEntityDetail,
    "entityactions" : BlocEntityActions
}


export class DynamicContentBloc extends React.Component<DynamicContentBlocProps, any> {
    static childContextTypes : React.ValidationMap<any> = {
        blocTheme: React.PropTypes.object,
        blocContext : React.PropTypes.object
    };

    getChildContext(): any {
        return {
            blocTheme: this.props.theme,
            blocContext : this.props.context
        };
    }

    render(){
        var component = catalog[this.props.template.type];
        if (!component)
            component = BlocNotFound;

        return React.createElement(
                component,
                {
                    template : this.props.template,
                    baseStyle : {},
                    user : this.props.user,
                    event : this.props.event,
                    visualTheme : this.props.visualTheme,
                    theme : this.props.theme,
                    context : this.props.context,
                    i18nService : this.props.i18nService,
                    className : "dynamiccontentbloc"
                }
            );
    }
}


