import * as React from 'react';
import * as _ from 'lodash';
import './toggle.less';

export interface ToggleProps {
    checked: boolean;
    defaultChecked: boolean;
    name: string;
    value: string;
    id: string;
}

export class Toggle extends React.Component<any, any> {
    constructor(props) {
        super(props);

        var checked = false;
        if ("checked" in this.props) {
            checked = this.props.checked;
        } else if ("defaultChecked" in this.props) {
            checked = this.props.defaultChecked;
        }

        this.state = {
            checked: !!checked,
            hasFocus: false
        };
    }

    componentWillReceiveProps(nextProps) {
        if ("checked" in nextProps) {
            this.setState({ checked: !!nextProps.checked });
        }
    }

    handleClick(event) {
        var checkbox = this.refs["input"] as any;
        //if (event.target !== checkbox) {
          event.preventDefault();
          event.stopPropagation();
          checkbox.focus();
          checkbox.checked = !checkbox.checked;
          this.props.onChange(checkbox.checked);          
          //return;
        //}

        if (!("checked" in this.props)) {
          this.setState({ checked: checkbox.checked });
        }
    }

    handleFocus() {
        this.setState({ hasFocus: true });
    }

    handleBlur() {
        this.setState({ hasFocus: false });
    }

    render() {
        var classes = ["toggleswitch"];
        if (this.state.checked) classes.push("toggleswitch--checked");
        if (this.state.hasFocus) classes.push("toggleswitch--focus");
        if (this.state.disabled) classes.push("toggleswitch--disabled");


        return <div className={ classes.join(" ") } onClick={ this.handleClick.bind(this) }>
        	<div className="toggleswitch-track">
        	    <div className="toggleswitch-track-check">
        	        <Check />
        	    </div>
        	    <div className="toggleswitch-track-x">
                    <X />
                </div>
        	</div> 
            <div className="toggleswitch-thumb"></div>
            <input ref="input" type="checkbox"
                className="toggleswitch-screenreader-only"
                onFocus={this.handleFocus.bind(this)}
                onBlur={this.handleBlur.bind(this)}
                onChange={this.props.onChange}
                />
        </div>
    }
}

class Check extends React.Component<any, any> {
    render() {
        return React.createElement(
            "svg",
            { width: "14", height: "11", viewBox: "0 0 14 11", xmlns: "http://www.w3.org/2000/svg" },
            React.createElement(
                "title",
                null,
                "switch-check"
            ),
            React.createElement("path", { d: "M11.264 0L5.26 6.004 2.103 2.847 0 4.95l5.26 5.26 8.108-8.107L11.264 0", fill: "#fff", "fill-rule": "evenodd" })
        );
    }
}

class X extends React.Component<any, any> {
    render() {
        return React.createElement(
            "svg",
            { width: "10", height: "10", viewBox: "0 0 10 10", xmlns: "http://www.w3.org/2000/svg" },
            React.createElement(
                "title",
                null,
                "switch-x"
            ),
            React.createElement("path", { d: "M9.9 2.12L7.78 0 4.95 2.828 2.12 0 0 2.12l2.83 2.83L0 7.776 2.123 9.9 4.95 7.07 7.78 9.9 9.9 7.776 7.072 4.95 9.9 2.12", fill: "#fff", "fill-rule": "evenodd" })
        );
    }
}


