import * as React from 'react';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {actions as eventActions} from '../services/eventservice';
import {actions as visualStateActions} from '../services/visualstateservice';
import {ContainerQuery} from '../sharedcomponents/containerquery';
import {BurgerMenu} from '../sharedcomponents/burgermenu';
import {FrontAppHeader} from './front.appheader';
import {FrontAppFooter} from './front.appfooter';
import {FrontMenu} from './front.menu';

import './front.appshell.less';

var mediaquery = {
    XXS: {
        minWidth: 200
    },    
    XS: {
        minWidth: 468
    },            
    S: {
        minWidth: 768,
    },  
    M: {
        minWidth: 1024,
    },  
    L: {
        minWidth: 1366,
    },       
    XL: {
        minWidth: 1600
    }
};

interface FrontAppShellProps{
    user : States.IAppUserState;
    event : States.ICurrentEventState;
    visualTheme : States.IEventVisualThemeState;
    i18n : States.i18nState;
    eventActions : typeof eventActions;
    visualStateActions : typeof visualStateActions;
    children : any;
    location : any;
    visualstate: any;
    params : any;
    routeParams : any;
}

class FrontAppShellComponent extends React.Component<FrontAppShellProps, any> {
    constructor(props){
        super(props);
        this.state = {
            menu : {
                collapsed : true
            }
        }
    }

    setVisualState(states){
        this.props.visualStateActions.currentStates(states);
    }
    
    toggleMenu(arg){
        this.setState({ 
            menu : _.assign({}, this.state.menu, {
                collapsed : !this.state.menu.collapsed
            })
        })
    }

    render() {
        var childs = React.cloneElement(this.props.children, {
            key: this.props.location.pathname,
            visualstate : this.props.visualstate,
            location : this.props.location,
            params  : this.props.params,
            routeParams  : this.props.routeParams,
            user : this.props.user.identity,
            event : this.props.event,
            visualTheme : this.props.visualTheme,

            i18n : this.props.i18n
        });

        var menuStyle = {
            backgroundColor : this.props.visualTheme.theme.primaryColor
        }

        return <ContainerQuery className={"front-app-shell"  + (this.props.user.identity.login && this.props.event ? "": " nologin")} stateName="appstate" query={ mediaquery } stateChanged={ (states) => { this.setVisualState(states); }}>
               <div className="front-app-shell-layout">
                    <FrontAppHeader event={this.props.event} visualTheme={this.props.visualTheme} />
                    <div className="front-page-container">
                        <ReactCSSTransitionGroup component="div" className="front-page-wrapper"
                            transitionName="pagestransition"
                            transitionEnterTimeout={600}
                            transitionLeaveTimeout={300}>
                            {childs}
                        </ReactCSSTransitionGroup>  
                    </div>
                    <FrontMenu collapsed={ this.state.menu.collapsed } event={this.props.event} visualTheme={this.props.visualTheme} onOverlayClick={ this.toggleMenu.bind(this) } />
                    <BurgerMenu collapsed={ this.state.menu.collapsed } onClick={ this.toggleMenu.bind(this) } style={menuStyle} />
                </div>
                
        </ContainerQuery>
    }
}


function mapStateToProps(state: States.IAppState) {
    return { 
        user: state.user,
        event : state.event.current,
        visualTheme : state.event.visualTheme,
        i18n : state.i18n,
        visualstate: state.visualstate
    }
}

function mapDispatchToProps(dispatch) {
  return {
    visualStateActions: bindActionCreators(visualStateActions, dispatch)
  }
}

export var FrontAppShell: new (any) => React.Component<any, any> = connect(mapStateToProps, mapDispatchToProps)(FrontAppShellComponent as any) as any;


