import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {BlocTitle, DynamicBloc} from './common';

export class BlocNews extends React.Component<any, any> {
    render(){
        return <DynamicBloc {...this.props}><div className={"bloc-news " + this.props.className } style={this.props.baseStyle}>
            <BlocTitle  {...this.props} />
        </div>
        </DynamicBloc>
    }
}


