import * as React from 'react';
import {AppPage}  from '../../sharedcomponents/page';
import {DynamicPage} from '../dynamicpage';
import {connect} from 'react-redux';

import './speakerdetail.less';

export interface SpeakerDetailPageProps{
    user? : States.IAppUserState;
    event? : States.ICurrentEventState;
    visualTheme? : States.IEventVisualThemeState;
    templates : States.IEventTemplatesState,
    speakers : States.IEventSpeakersState,
    entities : States.IEventEntitiesState
}

class SpeakerDetailPageComponent extends React.Component<SpeakerDetailPageProps, any> {
    render(){
        var speakerid = (this.props as any).params.speakerid;
        var speaker = this.props.speakers.speakers.filter((t) => t.id == speakerid)[0];
        var context = {
            speaker : speaker,
            entity : speaker,
            entiySchema : this.props.entities.schemas.speaker 
        }
        return <DynamicPage template="speaker" context={context} user={this.props.user} event={this.props.event} visualTheme={ this.props.visualTheme } templates={this.props.templates} />
    }
}

function mapStateToProps(state: States.IAppState) {
    return { 
        user: state.user,
        event : state.event.current,
        templates : state.event.templates,
        speakers : state.event.speakers,
        entities : state.event.entities,
    }
}

export var SpeakerDetailPage: new (any) => React.Component<SpeakerDetailPageProps, any> = connect(mapStateToProps)(SpeakerDetailPageComponent as any) as any;


