import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {Link} from 'react-router';
import {connect} from 'react-redux';
import { AppPage, AppPageHeader } from '../../sharedcomponents/page';
import {AvailableBlocs} from './contenteditor.availableblocs';
import {EntityBlocs} from './contenteditor.blocs';
import {contentEditorCatalog} from '../../../data/fakedata';
import './contenteditor.less';

export interface ContentEditorProps {
    user? : States.IAppUserState;
    visualTheme? : States.IEventVisualThemeState;
    event? : States.ICurrentEventState;
    templates : States.IEventTemplatesState;
    i18n? : States.i18nState;
}

class ContentEditorComponent extends React.Component<ContentEditorProps, any> {
    constructor(props : ContentEditorProps){
        super(props);
    }

    render(){        
        return <AppPage className="contenteditorpage">
            <AppPageHeader i18n="entities.contenteditor.title">
                <Link role="button" to={"/preview/" + this.props.event.id + "/"}>Test</Link>
            </AppPageHeader>
            <ContentEditorSection {...this.props} />
        </AppPage>
    }
}

function mapStateToProps(state : States.IAppState) {
    return { 
        speakers: state.event.speakers,
        templates : state.event.templates,
        visualTheme : state.event.visualTheme
    }
}

export var ContentEditor: new (any) => React.Component<any, any> = connect(mapStateToProps)(ContentEditorComponent as any) as any;

export class ContentEditorSection extends React.Component<ContentEditorProps, any> {
    constructor(props){
        super(props);
        this.dropCanceled = this.dropCanceled.bind(this);

        this.state = {
            fields : contentEditorCatalog,
            schema : this.props.templates.templates["home"]
        }
    }

    dropCanceled(){
        var fields = this.refs["fields"] as any;
        console.log("drop canceled");
        fields.decoratedComponentInstance.restoreFields();
    }
    
    render(){    
        return <section>
            <AvailableBlocs fields={ this.state.fields } dropCanceled={this.dropCanceled} />
            <EntityBlocs ref="fields" i18n={this.props.i18n} event={ this.props.event } schema={ this.state.schema } visualTheme={this.props.visualTheme}  />
        </section>
    }
}



