import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { IContentEditorEditorProps } from './common.tsx';

export class NotSupportedEditor extends React.Component<IContentEditorEditorProps, any> {
    render(){
        return <div className="contenteditorbloc">
            {this.props.bloc.type} not yet supported
        </div>
    }
}


