import { exhibitorsList } from '../../fakedata/exhibitors';

var INITIAL_STATE =<States.IEventExhibitorsState> {
    loading : true,
    exhibitors : []    
};

export function eventExhibitorsReducer(state = INITIAL_STATE, action = { type: '', payload: <any>null }) {
    switch (action.type) {
        case "EVENT_CURRENT": {
            return _.assign({}, state, { 
                loading : false,
                exhibitors : exhibitorsList
            });
        }

        default:
            return state;
    }
}

export var utils = {

}


