import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {DynamicBloc} from './common';

export class BlocNotFound extends React.Component<any, any> {
    render(){
        return <DynamicBloc {...this.props}><div className={"bloc-news " + this.props.className } style={this.props.baseStyle}>
            Oupsss
        </div>
        </DynamicBloc>
    }
}


