import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {BlocTitle,DynamicBloc, DynamicContentBlocProps} from './common';

import './bloc.pictureteaser.less';

export class BlocPictureTeaser extends React.Component<DynamicContentBlocProps, any> {
    render(){    
        var template = this.props.template;
        return <DynamicBloc {...this.props}>
            <div className={"dynamic-bloc-pictureteaser " + this.props.className }>
                <BlocTitle  {...this.props} />
            </div>
        </DynamicBloc>
    }
}


