import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {DynamicBloc, DynamicContentBlocProps} from './common';

import './bloc.entitydetail.less';

export class BlocEntityDetail extends React.Component<DynamicContentBlocProps, any> {
    render(){    
        var template = this.props.template;
        var header, content;
        var properties = this.props.template.properties as PageBlocs.IEntityDetailProperties;
        if (properties.header){
            header = <EntityDetailBlocs {...this.props} className={"bloc-header"} data={properties.header} />
        }

        if (properties.content){
            content = <EntityDetailBlocs {...this.props} className={"bloc-content"} data={properties.content} />
        }

        return <DynamicBloc {...this.props}>
            <div className={"dynamic-bloc-entitydetail " + this.props.className }>
                {header}
                {content}
            </div>
        </DynamicBloc>
    }
}

interface EntityDetailBlocsContainerProps extends DynamicContentBlocProps {
    data : PageBlocs.IEntityDetailBlocProperties[];    
}

class EntityDetailBlocs extends React.Component<EntityDetailBlocsContainerProps, any> {
    render(){    
        var entity = this.props.context.entity;
        var content;
        if (entity){
            content = this.props.data.map((bloc, index) => {
                return <EntityDetailBloc key={"bloc" + index} {...this.props} entitybloc={bloc} />
            });
        }
        return <div className={this.props.className + " entitydetailblocs" }>
            {content}
        </div>
    }
}

interface EntityDetailBlocContainerProps extends DynamicContentBlocProps {
    entitybloc : PageBlocs.IEntityDetailBlocProperties;    
}

class EntityDetailBloc extends React.Component<EntityDetailBlocContainerProps, any> {
    render(){            
        var bloctype = this.props.entitybloc.type;
        var classes = [];
        var blocStyle = {} as any;

        if (bloctype == "picturebubble"){
            return <EntityDetailPicture {...this.props} bubble={true}/>
        }
        else if (bloctype == "picture"){
            return <EntityDetailPicture {...this.props} bubble={false}/>
        }
        else if (bloctype == "text"){
            bloctype = "div";
            classes.push("text");
        }
        else if (bloctype == "h1"){
            if (this.props.theme){
                blocStyle.color = this.props.theme.titleColor;
            }
        }        

        var content = [];
        this.props.entitybloc.fields.forEach((fld, idx) => {
            if (idx > 0){
                content.push(<span className="sep" key={ "sep" + idx }> </span>);
            }
            content.push(<EntityDetailField key={ "field" + idx } {...this.props} field={fld} />);
        });

        return React.createElement(bloctype, {
            className : classes.join(" "),
            style : blocStyle
        }, content);
    }
}

interface EntityDetailFieldProps extends DynamicContentBlocProps {
    field : PageBlocs.IEntityDetailBlocFieldProperties;    
}

class EntityDetailField extends React.Component<EntityDetailFieldProps, any> {
    render(){    
        var entity = this.props.context.entity;
        var entitySchema = this.props.context.entity;
        return <span className="field">{ entity[this.props.field.name]}</span>
    }
}

interface EntityDetailPictureProps extends EntityDetailBlocContainerProps {
    bubble : boolean;
}

class EntityDetailPicture extends React.Component<EntityDetailPictureProps, any> {
    render(){    
        var entity = this.props.context.entity;
        var entitySchema = this.props.context.entity;
        var picStyle = {} as any;
        var url = entity[this.props.entitybloc.fields[0].name];
        if (url) {
            picStyle.backgroundImage = 'url("' + url + '")';
            return <div className={ "picture" + ( this.props.bubble ? " bubble" : "" )} style={picStyle}></div>
        }

        return <div className="empty"></div>
    }
}


