import { eventTheme } from '../../fakedata/visualtheme';

var INITIAL_STATE = <States.IEventVisualThemeState>{
    loading : true,
    theme: null
} as States.AppThemeState;


export function eventVisualThemeReducer(state = INITIAL_STATE, action = { type: '', payload: <any>null }) {
    switch (action.type) {
        case "EVENT_CURRENT": {
            return _.assign({}, state, { 
                loading : false,
                theme : eventTheme 
            });
        }

        default:
            return state;
    }
}

export var utils = {

}


