import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {injectcontext} from '../../utils/decorators';
import {connect} from 'react-redux';
import {DynamicBloc, BlocTitle, DynamicContentBlocProps, IDynamicContentItemsBlocProperties} from './common';
import * as Slider from 'react-slick';
import './bloc.comingsessions.less';

interface BlocComingSessionsProps extends DynamicContentBlocProps{
    sessions : States.IEventSessionsState
}

class BlocComingSessionsComponent extends React.Component<BlocComingSessionsProps, any> {
    render() {
        var properties = (this.props.template.properties || {}) as IDynamicContentItemsBlocProperties;
        if (properties.itemsDisplayMode == "carousel"){
            return <BlocComingSessionsCarousel {...this.props} />
        } else if (properties.itemsDisplayMode == "list"){
            return <BlocComingSessionsList {...this.props} />
        } else {
            return <BlocComingSessionsList {...this.props} />
        }
    }
}

function mapStateToProps(state : States.IAppState) {
    return { 
        sessions: state.event.sessions
    }
}

export var BlocComingSessions: new (any) => React.Component<any, any> = connect(mapStateToProps)(BlocComingSessionsComponent as any) as any;

class BlocComingSessionsList extends React.Component<BlocComingSessionsProps, any> {
    render() {
        var properties = (this.props.template.properties || {}) as IDynamicContentItemsBlocProperties;
        var speakers;
        if (this.props.event && this.props.sessions) {
            var list = this.props.sessions.sessions;
            if (properties.maxItems) {
                list = list.slice(0, properties.maxItems);
            }

            speakers = list.map((session) => {
                return <BlocComingSessionsItem session={session} event={this.props.event} template={ this.props.template }  key={session.id} />;
            })
        }

        return <DynamicBloc {...this.props}><div className={"bloc-comingsessions list " + this.props.className }>
            <BlocTitle {...this.props} />
            <div className="bloc-comingsessions-items bloc-content">
                {speakers}
            </div>
        </div>
        </DynamicBloc>
    }
}

class BlocComingSessionsCarousel extends React.Component<BlocComingSessionsProps, any> {
    render() {
        var settings = {
            dots: true,
            infinite: true,
            speed: 500,
            slidesToShow: Math.min(this.props.template.properties.maxItems, 5),
            slidesToScroll: Math.min(this.props.template.properties.maxItems, 5)
        };

        var speakers;
        if (this.props.event && this.props.sessions) {
            var list = this.props.sessions.sessions;
            if (this.props.template.properties.maxItems) {
                list = list.slice(0, this.props.template.properties.maxItems);
            }

            speakers = list.map((session) => {
                return <div className="sessionslide" key={session.id}>
                    <BlocComingSessionsItem session={session} event={this.props.event} template={ this.props.template } />
                </div>
            })
        }

        return <DynamicBloc {...this.props}><div className={"bloc-comingsessions carousel " + this.props.className }>
            <BlocTitle  {...this.props} />
            <Slider {...settings} children={speakers} className="bloc-content">
            </Slider>
            <div className="clearfix"></div>
        </div>
        </DynamicBloc>
    }
}

interface BlocComingSessionsItemProps{
    session : Entities.ISession;
    event : States.ICurrentEventState;
    template : PageBlocs.IBlocTemplateBase;
}

@injectcontext({ router : React.PropTypes.object })
class BlocComingSessionsItem extends React.Component<BlocComingSessionsItemProps, any> {
    constructor(props){
        super(props);
        this.openSession = this.openSession.bind(this);
    }

    openSession(){
        var session = this.props.session;
        var router = this.context["router"] as any;
        router.push("/preview/" + this.props.event.id + "/sessions/" + session.id);
    }

    render() {
        var session = this.props.session as any;
        var titlestyle;
        if (this.props.template.theme.preTitleColor){
            titlestyle = {
                color : this.props.template.theme.preTitleColor
            }
        }        
        
        return <div className="session clickable" onClick={ this.openSession }>
            <h4 className="session-title" style={titlestyle}>
                { session.title }
            </h4>
            <h5 className="session-category">
                { session.category }
            </h5>
        </div>
    }
}



