import * as Promise from 'bluebird';
import { participants, contacts } from '../../fakedata/participants';
import {combineReducers} from 'redux';

var INITIAL_STATE = {
    contacts : [],
    allparticipants: []
} as States.IParticipantsState;

export function participantsReducer(state = INITIAL_STATE, action = { type: '', payload: <any>null }) {
    switch (action.type) {
        case "EVENT_CURRENT": {
            return _.assign({}, state, { 
                contacts : contacts,
                allparticipants: participants
            });
        }

        default:
            return state;
    }
}

export var utils = {
    byId(all: Entities.IParticipant[], id:string) : Entities.IParticipant {
        return all.filter((p) => p.id == id)[0];
    },

    byIds(all: Entities.IParticipant[], idlist:string[]) : Entities.IParticipant[] {
        return idlist
            .map((id) => all.filter((p) => p.id == id)[0])
            .filter((p) => p != undefined);
    }
}


