export function userStatusReducer(state = {}, action = { type: '', payload: <any>null }):any {
    switch (action.type) {
        case "USER_CHECK": {
            return { loading : true, message:action.payload.message };
        }
        case "USER_DENIED": {
            return _.assign({}, state, { message : action.payload.message });
        }

        default:
            return state;
    }
}


