import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { IContentEditorEditorProps, CommonBlocProperties, CommonItemsBlocProperties } from './common';
import {AppLabel} from '../../services/i18nservice';


export class SpeakersEditor extends React.Component<IContentEditorEditorProps, any> {
    render(){
        this.props.bloc.properties = this.props.bloc.properties || {};

        return <div className={ this.props.className }>
           <CommonBlocProperties bloc={this.props.bloc}  visualTheme={this.props.visualTheme} schema={ this.props.schema } onChange={this.props.onChange} />
           <CommonItemsBlocProperties bloc={this.props.bloc}  visualTheme={this.props.visualTheme} schema={ this.props.schema } onChange={this.props.onChange} />           
        </div>
    }
}


