import { eventThematiques, eventThematiquesActions } from '../../fakedata/thematiques';

var INITIAL_STATE = <States.IEventThematiquesState>{
    loading : true,
    thematiques : [],
    thematiquesActions: []    
};

export function eventThematiquesReducer(state = INITIAL_STATE, action = { type: '', payload: <any>null }) {
    switch (action.type) {
        case "EVENT_CURRENT": {
            return _.assign({}, state, { 
                loading : false,
                thematiques : eventThematiques,
                thematiquesActions : eventThematiquesActions 
            });
        }

        default:
            return state;
    }
}

export var utils = {

}


