import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {DynamicContent} from '../../sharedcomponents/dynamiccontent/dynamiccontent';
import {Loader} from '../../sharedcomponents/loader';
import {connect} from 'react-redux';
import {DynamicPage} from '../dynamicpage';
import './home.less';

export interface HomePageProps {
    user? : States.IAppUserState;
    event? : States.ICurrentEventState;
    visualTheme? : States.IEventVisualThemeState;
    templates : States.IEventTemplatesState
}

export class HomePageComponent extends React.Component<HomePageProps, any> {
    render(){        
        return <DynamicPage template="home" user={this.props.user} event={this.props.event} visualTheme={ this.props.visualTheme } templates={this.props.templates} />
    }
}

function mapStateToProps(state: States.IAppState) {
    return { 
        user: state.user,
        event : state.event.current,
        templates : state.event.templates
    }
}

export var HomePage: new (any) => React.Component<any, any> = connect(mapStateToProps)(HomePageComponent as any) as any;


