import * as _ from 'lodash';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import {Loader} from './sharedcomponents/loader';
import {ContainerQuery} from './sharedcomponents/containerquery';
import {AppHeader} from './app.header';
import {AppMenu} from './app.menu';
import {BurgerMenu} from './sharedcomponents/burgermenu';
import { actions as eventActions} from './services/eventservice';
import {  actions as visualStateActions } from './services/visualstateservice';
import {injectcontext} from './utils/decorators';
import HTML5Backend from 'react-dnd-html5-backend';
import { DragDropContext } from 'react-dnd';

var ctx = DragDropContext as any;

var supportedLanguages = ["fr", "en"];

import './app.shell.less';

var mediaquery = {
    XXS: {
        minWidth: 200
    },
    XS: {
        minWidth: 468
    },
    S: {
        minWidth: 768,
    },
    M: {
        minWidth: 1024,
    },
    L: {
        minWidth: 1366,
    },
    XL: {
        minWidth: 1600
    }
};

var limitForFixedMenu = "L";

interface AppShellComponentProps {
    user: States.IAppUserIdentityState;
    event: States.IEventState;
    eventActions: typeof eventActions;
    visualStateActions: typeof visualStateActions;
}

@ctx(HTML5Backend)
export class AppShellComponent extends React.Component<AppShellComponentProps, any> {
    setVisualState(states) {
        this.props.visualStateActions.currentStates(states);
    }

    render() {
        return <ContainerQuery className={"app-shell" + (this.props.user.login && this.props.event.current && this.props.event.current.id ? "" : " nologin") } stateName="appstate" query={ mediaquery } stateChanged={ (states) => { this.setVisualState(states); } }>
            { React.createElement(AppShellContent, this.props) }
        </ContainerQuery>
    }
}

function mapStateToProps(state: States.IAppState) {
    return {
        user: state.user.identity,
        event: state.event,
        i18n: state.i18n
    }
}

function mapDispatchToProps(dispatch) {
    return {
        visualStateActions: bindActionCreators(visualStateActions, dispatch)
    }
}

export var AppShell: new (any) => React.Component<any, any> = connect(mapStateToProps, mapDispatchToProps)(AppShellComponent as any) as any;

export interface AppMenuState {
    collapsed: boolean;
    collapsedWhenLarge: boolean;
    allowFixed: boolean;
}

export interface AppShellState {
    menu: AppMenuState
}

export class AppShellContent extends React.Component<any, AppShellState> {
    static contextTypes: React.ValidationMap<any> = {
        appState: React.PropTypes.object
    };

    i18nDetach: any;

    constructor(props) {
        super(props);

        var isLarge = (this.props.visualstate.indexOf(limitForFixedMenu) >= 0);

        this.state = {
            menu: {
                allowFixed: isLarge,
                collapsed: !isLarge,
                collapsedWhenLarge: false
            }
        }
    }

    getChildContext(): any {
        var res = {
            appState: this.state
        };
    }

    componentWillReceiveProps(nextprops) {
        var wasLarge = (this.props.visualstate.indexOf(limitForFixedMenu) >= 0);
        var isLarge = (nextprops.visualstate.indexOf(limitForFixedMenu) >= 0);

        this.setState({
            menu: _.assign(this.state.menu, { allowFixed: isLarge })
        } as any);

        if (wasLarge && !isLarge) {
            this.setState({
                menu: _.assign(this.state.menu, { collapsed: true, collapsedWhenLarge: this.state.menu.collapsed })
            } as any);
        } else if (!wasLarge && isLarge) {
            this.setState({
                menu: _.assign(this.state.menu, { collapsed: this.state.menu.collapsedWhenLarge })
            } as any);
        }
    }

    showMenu(show: boolean) {
        if (!this.state.menu.allowFixed) {
            this.setState({
                menu: _.assign(this.state.menu, { collapsed: !show })
            } as any);
        }
    }

    toggleMenu() {
        console.log("toggle menu " + this.state.menu.collapsed)
        this.setState({
            menu: _.assign(this.state.menu, { collapsed: !this.state.menu.collapsed })
        } as any);
    }

    render() {
        var childs = React.cloneElement(this.props.children, {
            key: this.props.location.pathname,
            visualstate: this.props.visualstate,
            location: this.props.location,
            params: this.props.params,
            routeParams: this.props.routeParams,
            user: this.props.user,
            event: this.props.event.current,
            i18n: this.props.i18n
        });

        var applayoutClasses = ["app-layout"];
        if (!this.state.menu.allowFixed) {
            applayoutClasses.push("menu-collapsible");
        }

        if (this.state.menu.collapsed) {
            applayoutClasses.push("menu-collapsed");
        }

        var user = this.props.user as States.IAppUserIdentityState;
        var event = this.props.event as States.IEventState;

        var header, menu, burger;

        if (user && event && user.login && event.current && event.current.id) {
            header = <AppHeader user={user} event={event} />;
            menu = <AppMenu visualstate={this.props.visualstate} user={user} event={event} menustate={this.state.menu} onShow={ (show) => this.showMenu(show) } />;
            burger = <BurgerMenu collapsed={ this.state.menu.collapsed || this.state.menu.allowFixed } onClick={ this.toggleMenu.bind(this) } />;
        }

        return <div className={ applayoutClasses.join(" ") }>
            <div className="app-content">
                {header}

                <ReactCSSTransitionGroup component="section" className="app-pages"
                    transitionName="pagestransition"
                    transitionEnterTimeout={600}
                    transitionLeaveTimeout={300}>
                    {childs}
                </ReactCSSTransitionGroup>
            </div>
            {menu}
            {burger}
        </div>
    }
}


