import * as React from 'react';
import * as ReactDOM from 'react-dom';
import * as _ from 'lodash';

import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import {BlocTitle} from './common';
import {DynamicContentBlocProps, DynamicBloc} from './common';
import { AppLabel } from '../../services/i18nservice';
import { injectcontext } from '../../utils/decorators';
import { actions as preferencesActions } from '../../services/user.preferenceservice';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';

import './bloc.socialnetworking.less';

interface BlocSocialNetworkingProps extends DynamicContentBlocProps{
    preferences : States.IAppUserPreferencesState,
    thematiques : States.IEventThematiquesState,
    participants : States.IParticipantsState,
    preferencesActions : typeof preferencesActions
}

export class BlocSocialNetworkingComponent extends React.Component<BlocSocialNetworkingProps, any> {
    render(){
        var content;
        if (this.props.preferences.socialNetworking.hasChosenNetworkingOrNot){
            if (this.props.preferences.socialNetworking.acceptSocialNetworking){
                content = <Summary key="summary" {...this.props} />            
            }else{
                return <div className="empty"></div>
            }
        }else{
            content = <OptIn key="optin" {...this.props} />
        }

        return <DynamicBloc {...this.props}><div className={"bloc-socialnetworking " + this.props.className }>
            <BlocTitle  {...this.props} />
            <ReactCSSTransitionGroup component="section" className="bloc-content"
            	transitionName="fadetransition"
            	transitionEnterTimeout={300}
            	transitionLeave={false}>
            	{content}
            </ReactCSSTransitionGroup>  
        </div>
        </DynamicBloc>
    }
}

function mapStateToProps(state: States.IAppState) {
    return { 
        preferences: state.user.preferences,  
        thematiques : state.event.thematiques,
        participants : state.event.participants      
    }
}

function mapDispatchToProps(dispatch) {
  return {
    preferencesActions: bindActionCreators(preferencesActions, dispatch)
  }
}

export var BlocSocialNetworking: new (any) => React.Component<any, any> = connect(mapStateToProps, mapDispatchToProps)(BlocSocialNetworkingComponent as any) as any;


@injectcontext({ router : React.PropTypes.object })
class OptIn extends React.Component<BlocSocialNetworkingProps, any> {
    constructor(props){
        super(props);
        this.acceptSocialNetwork = this.acceptSocialNetwork.bind(this);
        this.refuseSocialNetwork = this.refuseSocialNetwork.bind(this);
    }

    setPref(accept : boolean){
        var router = this.context["router"] as any;
        var prefs = _.assign({}, this.props.preferences, { 
            socialNetworking : _.assign({}, this.props.preferences.socialNetworking,{
                hasChosenNetworkingOrNot : true,
                acceptSocialNetworking : accept
            })
        });
        this.props.preferencesActions.setPreferences(prefs);
    }

    acceptSocialNetwork(){
        var router = this.context["router"] as any;
        this.setPref(true);
        router.push('/preview/' + this.props.event.id + '/profile?tab=networking');
    }

    refuseSocialNetwork(){        
        this.setPref(false);
    }

    render(){
        var txt, remark, optin = this.props.template.properties.optin;
        var lng = "fr";

        if (optin.title && optin.title[lng]){
            txt = <div className="optindesc">{ optin.title[lng] }</div>;
        }
        if (optin.remark && optin.remark[lng]){
            remark = <div className="optinremark">{ optin.remark[lng] }</div>;
        }

        return <div className="bloc-content-panel optin">
            {txt}
            {remark}
            <div className="optinactions">
                <button onClick={this.acceptSocialNetwork}>Oui</button>
                <button  onClick={this.refuseSocialNetwork}>Non</button>
            </div>
        </div>
    }
}

class Summary extends React.Component<BlocSocialNetworkingProps, any> {
    render(){
        var themes, recommanded;
        var networking = this.props.preferences.socialNetworking;

        if (networking.socialNetworkingThemes && networking.socialNetworkingThemes.length){
            themes = <div>
                <h4>Mes thmatiques</h4>
                <div className="userthematiques">
                {networking.socialNetworkingThemes.map((t) => {
                    return <ThemeSummary key={t.thematiqueId} {...this.props} theme={t} />
                })}
                </div>
            </div>;
        }

        return <div className="bloc-content-panel">
            {themes}
            <RecommandedContacts {...this.props} />
        </div>
    }
}

class RecommandedContacts extends React.Component<BlocSocialNetworkingProps, any> {
    render(){
        var recommandedItems = this.props.participants.allparticipants.slice(0, 3); 
        var recommanded = recommandedItems.map((p) => {
            return <ContactItem key={p.id} {...this.props} contact={p}/>
        });

        return <div className="recommanded">
            <h4>Prenez contact avec</h4>
            <div className="contacts">
                { recommanded }
            </div>
            <div className="search">
                <button>Chercher un participant</button>
                <button>Toutes les recommandations</button>
            </div>
        </div>
    }
}

interface ContactItemProps extends BlocSocialNetworkingProps {
    contact : Entities.IParticipant;
}

@injectcontext({ router : React.PropTypes.object })
class ContactItem extends React.Component<ContactItemProps, any> {
    constructor(props){
        super(props);
        this.openContact = this.openContact.bind(this);
    }

    openContact(){
        var router = this.context["router"] as any;        
        router.push('/preview/' + this.props.event.id + '/contact/' + this.props.contact.id);
    }

    render(){
        var contact = this.props.contact;
        var contactPicStyle = {} as any;
        if (contact.photo) {
            contactPicStyle.backgroundImage = 'url("' + contact.photo + '")';
        }

        return <div className="contact clickable" onClick={this.openContact}>
            <div className="contact-pic" style={contactPicStyle}></div>
            <div className="contact-name">
                { contact.firstname + " " + contact.lastname}
            </div>
            <div className="contact-company">
                { contact.company}
            </div>
            <div className="contact-title">
                { contact.title}
            </div>
        </div>
    }
}


interface ThemeSummaryProps extends BlocSocialNetworkingProps {
    theme : Entities.IAppUserThematique;
}

@injectcontext({ router : React.PropTypes.object })
class ThemeSummary extends React.Component<ThemeSummaryProps, any> {
    render(){
        var theme = this.props.thematiques.thematiques.filter((t) => t.id == this.props.theme.thematiqueId)[0];
        var router = this.context["router"] as any;
        
        return <div className="themesummary clickable" onClick={ (arg) => { router.push("/preview/" + this.props.event.id + "/thematique/" + this.props.theme.thematiqueId )}}>
            { theme.title }
        </div>
    }
}


