import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { DragSource, DropTarget } from 'react-dnd';
import * as ReactCSSTransitionGroup from 'react-addons-css-transition-group';

import {NotSupportedEditor} from './editor.notsupported';
import {BoolEditor} from './editor.bool';
import {TextEditor} from './editor.text';
import {SelectListEditor} from './editor.selectlist';

import './entityschema.fields.less';

var editors = {
    "selectlist": SelectListEditor,
    "bool": BoolEditor,
    "text": TextEditor,
}

interface EntityFieldsProps {
    schema: IEntitySchema;
    connectDropTarget? : any; //injected by react-dnd
    isOver? : boolean; //injected by react-dnd
}

const listTarget = {
    drop(props, monitor?: any, component?) {
        const draggedItem = monitor.getItem();
        if (draggedItem.isFieldDefinition) {
            console.log("drop", draggedItem);
            component.applyFields(draggedItem);
        }/*else{
            component.restoreFields();
        }*/
    },

    hover(props, monitor?: any, component?) {
        const draggedItem = monitor.getItem();
        if (component.state.editedField){
            component.setState({ editedField : null });
        }
        if (draggedItem.isFieldDefinition && component.state.insertedAt == null) {
            console.log("hovering from list");
            component.insertAt(props.schema.fields.length, draggedItem)
        }
    }
}

@DropTarget('fielditem', listTarget, (connect, monitor) => ({
    connectDropTarget: connect.dropTarget(),
    isOver : monitor.isOver()
}))
export class EntityFields extends React.Component<EntityFieldsProps, any> {
    constructor(props: EntityFieldsProps) {
        super(props);
        this.moveCard = this.moveCard.bind(this);
        this.insertAt = this.insertAt.bind(this);
        this.applyFields = this.applyFields.bind(this);
        this.fieldChanged = this.fieldChanged.bind(this);
        this.editField = this.editField.bind(this);
        this.removeField = this.removeField.bind(this);

        this.state = {
            fieldsToDisplay: this.props.schema && this.props.schema.fields ? this.props.schema.fields.map((f, idx) => { return { index: idx, item: f } }) : [],
            editedField : null,
            insertedAt : null
        }
    }

    render() {
        const { connectDropTarget } = this.props;
        var fields;
        if (this.state.fieldsToDisplay) {
            fields = this.state.fieldsToDisplay.map((f, idx) => {
                return <EntityFieldItem key={f.item.key} item={f.item}
                    isFake={f.isFake} index={f.index}
                    editing={this.state.editedField}
                    moveCard={this.moveCard}
                    insertAt={this.insertAt}
                    fieldChanged={this.fieldChanged}
                    applyFields={this.applyFields}
                    editField={this.editField}
                    removeField={this.removeField} />
            });
            console.log("render " + this.state.fieldsToDisplay.map((f) => f.item.key).join(" "));
        }

        // if (this.state.editedField){
        //     return <div className="fieldslist">
        //         {fields}
        //     </div>
        // }

        return connectDropTarget(<div className="fieldslist">
            {fields}
        </div>)
    }

    fieldChanged(field : IEntityFieldSchema){
        //this.setState({});
    }

    editField(field : IEntityFieldSchema){
        this.setState({ editedField : field });
    }

    removeField(field : IEntityFieldSchema){
        var idx = this.props.schema.fields.indexOf(field);
        if (idx >= 0){
            this.props.schema.fields.splice(idx, 1);
        }
        this.setState({
            fieldsToDisplay: this.props.schema && this.props.schema.fields ? this.props.schema.fields.map((f, idx) => { return { index: idx, item: f } }) : []
        });
    }

    restoreFields() {
        console.log("restore fields");
        this.setState({
            insertedAt : null,
            fieldsToDisplay: this.props.schema && this.props.schema.fields ? this.props.schema.fields.map((f, idx) => { return { index: idx, item: f } }) : []
        });
    }

    applyFields(item) {
        var edited;
        console.log("apply fields");
        if (item.isFieldDefinition) {
            this.props.schema.fields = this.state.fieldsToDisplay.map((f, idx) => {
                if (f.isFake) {
                    edited = f.item;
                }
                return f.item;
            });
        } else {
            //TODO : mutation de l'objet en props == pas cool, faire remonter au parent
            this.props.schema.fields = this.state.fieldsToDisplay.map((f) => {
                return f.item;
            });
        }
        this.restoreFields();

        if (edited){
            setTimeout(() => {
                this.setState({
                    editedField : edited
                });    
            }, 0);
            
        }
    }

    insertAt(idx, item) {
        console.log("insert at " + idx, item);
        var srcfields = this.props.schema && this.props.schema.fields ? this.props.schema.fields : [];
        var res = [];
        var insert = (fIdx) => {
            res.push({
                index: fIdx,
                isFake: true,
                item: {
                    key: 'new-' + item.item.code + '-' + new Date().getTime(),
                    labels: {
                        fr: item.item.title
                    },
                    type: item.item.code
                }
            });
        }
        srcfields.forEach((f, fIdx) => {
            if (fIdx == idx) {
                insert(fIdx);
            }

            res.push({ index: fIdx, item: f });
        });
        
        if (idx == srcfields.length) {
        	insert(srcfields.length);
        }

        this.setState({ fieldsToDisplay: res, insertedAt : idx });
    }

    moveCard(dragIndex, hoverIndex) {
        console.log("move " + dragIndex + "->" + hoverIndex);
        var srcfields = this.state.fieldsToDisplay;
        var draggedfield = srcfields[dragIndex];
        var hovered = srcfields[hoverIndex];
        var fields = [];
        if (dragIndex > hoverIndex) {
            srcfields.forEach((f, idx) => {
                if (f == hovered) {
                    fields.push(draggedfield);
                }
                if (f != draggedfield)
                    fields.push(f);
            });
        } else {
            srcfields.forEach((f, idx) => {
                if (f != draggedfield)
                    fields.push(f);
                if (f == hovered) {
                    fields.push(draggedfield);
                }
            });
        }
        fields.forEach((f, idx) => {
            f.index = idx;
        });
        console.log("result " + fields.map((f) => f.key).join(" "));
        this.setState({ fieldsToDisplay: fields });
    }
}


const cardSource = {
    beginDrag(props) {
        return {
            id: props.item.key,
            index: props.index,
            isFieldDefinition: false
        };
    }
};

const cardTarget = {
    drop(props, monitor?: any, component?) {
        const draggedItem = monitor.getItem();
        console.log("drop", draggedItem);
        props.applyFields(draggedItem);
    },

    hover(props, monitor?: any, component?) {
        const draggedItem = monitor.getItem();
        //console.log("moving", draggedItem);
        //console.log("hovered", props);
        if (props.isFake)
            return;

        if (draggedItem.isFieldDefinition) {
            const hoverIndex = props.index;
            // Determine rectangle on screen
            const hoverBoundingRect = ReactDOM.findDOMNode(component).getBoundingClientRect();
            // Get vertical middle
            const hoverMiddleY = (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2;
            // Determine mouse position
            const clientOffset = monitor.getClientOffset();
            // Get pixels to the top
            const hoverClientY = clientOffset.y - hoverBoundingRect.top;

            if (hoverClientY < hoverMiddleY) {
                if (draggedItem.dragindex == hoverIndex)
                    return;
                draggedItem.dragindex = hoverIndex;
                props.insertAt(hoverIndex, draggedItem);
                return;
            }

            if (hoverClientY > hoverMiddleY) {
                if (draggedItem.dragindex == hoverIndex + 1)
                    return;
                draggedItem.dragindex = hoverIndex + 1;
                props.insertAt(hoverIndex + 1, draggedItem);
                return;
            }

        } else {
            const dragIndex = draggedItem.index;
            const hoverIndex = props.index;
            // Don't replace items with themselves
            if (dragIndex === hoverIndex) {
                return;
            }

            // Determine rectangle on screen
            const hoverBoundingRect = ReactDOM.findDOMNode(component).getBoundingClientRect();
            // Get vertical middle
            const hoverMiddleY = (hoverBoundingRect.bottom - hoverBoundingRect.top) / 2;
            // Determine mouse position
            const clientOffset = monitor.getClientOffset();
            // Get pixels to the top
            const hoverClientY = clientOffset.y - hoverBoundingRect.top;

            // Only perform the move when the mouse has crossed half of the items height
            // When dragging downwards, only move when the cursor is below 50%
            // When dragging upwards, only move when the cursor is above 50%

            // Dragging downwards
            if (dragIndex < hoverIndex && hoverClientY < hoverMiddleY) {
                return;
            }

            // Dragging upwards
            if (dragIndex > hoverIndex && hoverClientY > hoverMiddleY) {
                return;
            }

            // Time to actually perform the action
            props.moveCard(dragIndex, hoverIndex);

            // Note: we're mutating the monitor item here!
            // Generally it's better to avoid mutations,
            // but it's good here for the sake of performance
            // to avoid expensive index searches.
            monitor.getItem().index = hoverIndex;
        }
    }
};


@DropTarget('fielditem', cardTarget, connect => ({
    connectDropTarget: connect.dropTarget()
}))
@DragSource('fielditem', cardSource, (connect, monitor) => ({
    connectDragSource: connect.dragSource(),
    isDragging: monitor.isDragging()
}))
export class EntityFieldItem extends React.Component<any, any> {
    constructor(props) {
        super(props);
        this.editField = this.editField.bind(this);
        this.exitEdit = this.exitEdit.bind(this);
        this.removeField = this.removeField.bind(this);
        this.state = {
            edit: false
        }
    }

    editField() {
        this.props.editField(this.props.item);
    }

    exitEdit() {
        this.props.editField(null);
    }

    removeField() {
        this.props.removeField(this.props.item);
    }

    render() {
        var field = this.props.item as IEntityFieldSchema;
        const { isDragging, connectDragSource, connectDropTarget } = this.props;
        const opacity = isDragging ? 0.3 : 1;
        var isEditing = this.props.editing == this.props.item;
        var actions, editor;
        if (!this.props.isFake) {
            actions = <div className="actions">
                <button className="dripicons-pencil" onClick={ this.editField }></button>
                <button className="dripicons-trash" onClick={ this.removeField }></button>
            </div>
        }

        if (isEditing) {
            var editorctrl = editors[field.type];
            if (!editorctrl)
                editorctrl = NotSupportedEditor;

            editor = <div className="fieldeditor back-dataform">
                { React.createElement(editorctrl, { 
                    field: field,
                    className : "fasttransition",
                    onChange : (field : IEntityFieldSchema) => {
                        console.log("field " + field.key + " changed");
                        this.props.fieldChanged(field);
                        this.setState({});
                    }
                 }) }
            </div>


            actions = <div className="actions">
                <button className="dripicons-cross" onClick={this.exitEdit }></button>
                <button className="dripicons-trash" onClick={ this.removeField }></button>
            </div>
        }

        var content = <div className="itemwrapper">
            <div className={ "fielditem card defaultPadding" + (this.props.isFake ? " fakeitem" : "") } style={{ opacity: opacity }}>
                <header>
                    <div className="title">{field.labels.fr}</div>
                    {actions}
                </header>
                <ReactCSSTransitionGroup component="section"
                    transitionName="simplefadetransition"
                    transitionEnterTimeout={200}
                    transitionLeave={false}>
                    {editor}
                </ReactCSSTransitionGroup>
            </div>
        </div>;
        
        if (this.props.editing){
            return content;
        }

        return connectDragSource(connectDropTarget(content));
    }
}


