import * as React from 'react';
import * as ReactDOM from 'react-dom';
import './entityschema.availablefields.less';
import { DragSource, DropTarget } from 'react-dnd';
import {AppLabel} from '../../services/i18nservice';

export class AvailableFields extends React.Component<any, any> {
    render(){
        var fields = this.props.fields;
        var fieldItems;
        if (fields){
            fieldItems = fields.map((f) => {
                return <AvailableFieldItem key={f.code} item={f} dropCanceled={ this.props.dropCanceled } />
            })
        }
        return <div className="availablefields">
            {fieldItems}
        </div>
    }
}

const cardSource = {
  beginDrag(props, monitor, component) {
    return {
      id: props.item.code,
      item : props.item,
      isFieldDefinition : true
    };
  },

  endDrag(props, monitor, component){
      var item = monitor.getItem();
      var res = monitor.getDropResult();
      var didDrop = monitor.didDrop();
      console.log("end drag did drop " + didDrop, res);
      var e = res;
      if (!didDrop){
      	props.dropCanceled();
      }
  }
};

@DragSource('fielditem', cardSource, (connect, monitor) => ({
  connectDragSource: connect.dragSource(),
  isDragging: monitor.isDragging()
}))
export class AvailableFieldItem extends React.Component<any, any> {
    render(){        
        const { isDragging, connectDragSource } = this.props;
        const opacity = isDragging ? 0 : 1;
        return connectDragSource(<div className="availablefield card defaultPadding item dark">
            <AppLabel i18n={ "entityschema.field." + this.props.item.code} />
        </div>)
    }
}


