import { createStore, applyMiddleware, compose } from 'redux';

import thunk from 'redux-thunk';
import { routerMiddleware } from 'react-router-redux';

import promiseMiddleware from './promise';
import {rootReducer} from '../services';

function configureStore(routerHistory, initialState) {
  const store = createStore(rootReducer, 
      compose(applyMiddleware(..._getMiddleware(routerHistory)), ..._getEnhancers())
    );
  if (__DEV__)
    console.log("DEV mode");
  else
    console.log("PROD mode");
  _enableHotLoader(store);
  return store;
}

function _getMiddleware(routerHistory) {
  let middleware = <any>[
    routerMiddleware(routerHistory),
    promiseMiddleware,
    thunk,
  ];

  return middleware;
}

function _getEnhancers() {
  let enhancers = [
    //persistState('session', _getStorageConfig()),
  ];

  if (__DEV__ && window.devToolsExtension) {
    enhancers = [...enhancers, window.devToolsExtension() ];
  }

  return enhancers;
}

function _enableHotLoader(store) {
  if (__DEV__ && module.hot) {
    module.hot.accept('../services', () => {
      const nextRootReducer = require('../services');
      store.replaceReducer(nextRootReducer);
    });
  }
}

// function _getStorageConfig() {
//   return {
//     key: 'react-redux-seed',
//     serialize: (store) => {
//       return store && store.session ?
//         JSON.stringify(store.session.toJS()) : store;
//     },
//     deserialize: (state) => ({
//       session: state ? fromJS(JSON.parse(state)) : fromJS({}),
//     }),
//   };
// }

export default configureStore;



