import * as Promise from 'bluebird';
import {combineReducers} from 'redux';
import {push} from 'react-router-redux';

import {eventEntitiesReducer} from './event.entityservice';
import {eventExhibitorsReducer} from './event.exhibitorservice';
import {eventPagesReducer} from './event.pageservice';
import {participantsReducer} from './event.participantservice';
import {eventSessionsReducer} from './event.sessionservice';
import {eventSpeakersReducer} from './event.speakerservice';
import {eventThematiquesReducer} from './event.thematiqueservice';
import {eventVisualThemeReducer} from './event.visualthemeservice';
 
var INITIAL_STATE = <States.IEventState>{
    current: null,
    entities : null,
    exhibitors:null,
    sessions : null,
    speakers : null,
    templates : null,
    thematiques : null,
    visualTheme : null
};

export var eventReducer = combineReducers({
    current : currentEventReducer,
    entities : eventEntitiesReducer,
    exhibitors : eventExhibitorsReducer,
    sessions : eventSessionsReducer,
    speakers : eventSpeakersReducer,
    participants : participantsReducer,
    templates : eventPagesReducer,
    thematiques : eventThematiquesReducer,
    visualTheme : eventVisualThemeReducer
});

export function currentEventReducer(state = { id : null, name : null }, action = { type: '', payload: <any>null }): any {
    switch (action.type) {
        case "EVENT_CURRENT": {
            var msg = <IAppUserEventShort> action.payload;
            return <States.ICurrentEventState>{
                loading : false,
                id : msg.id,
                name : msg.name
            };
        }

        default:
            return state;
    }
}


export var actions = {
    currentEvent(event : IAppUserEventShort, navigate = false){
        return (dispatch, state) => {
            dispatch({ type : "EVENT_CURRENT",  payload : event});    
            if (navigate){
                setTimeout(() => {
                    dispatch(push("/event/ " + event.id + "/"));
                }, 0);
            }        
        }
    }
}


