import * as React from 'react';
import {AppPage}  from '../../sharedcomponents/page';
import {DynamicPage} from '../dynamicpage';
import {connect} from 'react-redux';

import './exhibitordetail.less';

export interface ExhibitorDetailPageProps{
    user? : States.IAppUserState;
    event? : States.ICurrentEventState;
    visualTheme? : States.IEventVisualThemeState;
    templates : States.IEventTemplatesState,
    exhibitors : States.IEventExhibitorsState,
    entities : States.IEventEntitiesState
}

class ExhibitorDetailPageComponent extends React.Component<ExhibitorDetailPageProps, any> {
    render(){
        var exhibitorid = (this.props as any).params.exhibitorid;
        var exhibitor = this.props.exhibitors.exhibitors.filter((t) => t.id == exhibitorid)[0];
        var context = {
            exhibitor : exhibitor,
            entity : exhibitor,
            entiySchema : this.props.entities.schemas.speaker 
        }
        return <DynamicPage template="exhibitor" context={context} user={this.props.user} event={this.props.event} visualTheme={ this.props.visualTheme } templates={this.props.templates} />
    }
}


function mapStateToProps(state: States.IAppState) {
    return { 
        user: state.user,
        event : state.event.current,
        templates : state.event.templates,
         exhibitors : state.event.exhibitors,
        entities : state.event.entities,
    }
}

export var ExhibitorDetailPage: new (any) => React.Component<ExhibitorDetailPageProps, any> = connect(mapStateToProps)(ExhibitorDetailPageComponent as any) as any;


