import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {BlocTitle,DynamicBloc, DynamicContentBlocProps} from './common';
import {connect} from 'react-redux';
import {injectcontext} from '../../utils/decorators';

import './bloc.thematiquewall.less';

interface BlocThematiqueWallProps extends DynamicContentBlocProps {
    participants : States.IParticipantsState;
}

class BlocThematiqueWallComponent extends React.Component<BlocThematiqueWallProps, any> {
    render(){    
        var template = this.props.template;
        return <DynamicBloc {...this.props}>
            <div className={"dynamic-bloc-thematiquewall " + this.props.className }>
                <BlocThematiqueWallContacts {...this.props} />
                <BlocThematiqueWallMessages {...this.props} />
            </div>
        </DynamicBloc>
    }
}

class BlocThematiqueWallContacts extends React.Component<BlocThematiqueWallProps, any> {
    render(){            
        var recommandedItems = this.props.participants.allparticipants.slice(0, 4); 
        var recommanded = recommandedItems.map((p) => {
            return <ContactItem key={p.id} {...this.props} contact={p}/>
        });

        return <div className="bloc-header">
            <h4>Prenez contact avec</h4>
            <div className="contacts">
                { recommanded }
            </div>
            <div className="actions">
                <button>Toutes les recommandations</button>
            </div>
        </div>
    }
}


interface ContactItemProps extends BlocThematiqueWallProps {
    contact : Entities.IParticipant;
}

@injectcontext({ router : React.PropTypes.object })
class ContactItem extends React.Component<ContactItemProps, any> {
    constructor(props){
        super(props);
        this.openContact = this.openContact.bind(this);
    }

    openContact(){
        var router = this.context["router"] as any;        
        router.push('/preview/' + this.props.event.id + '/contact/' + this.props.contact.id);
    }

    render(){
        var contact = this.props.contact;
        var contactPicStyle = {} as any;
        if (contact.photo) {
            contactPicStyle.backgroundImage = 'url("' + contact.photo + '")';
        }

        return <div className="contact clickable" onClick={this.openContact}>
            <div className="contact-pic" style={contactPicStyle}></div>
            <div className="contact-name">
                { contact.firstname + " " + contact.lastname}
            </div>
            <div className="contact-company">
                { contact.company}
            </div>
            <div className="contact-title">
                { contact.title}
            </div>
        </div>
    }
}

