class BlocThematiqueWallMessages extends React.Component<BlocThematiqueWallProps, any> {
    render(){        
        var messages = fakeMessages.map((msg) => {
                return <div key={msg.id} className="message">
                    <div className="message-body">
                        <div className="message-text">
                            {msg.text}
                        </div>
                        <div className="message-sender">
                            envoy par {msg.sender.fullname} {msg.date}
                        </div>
                    </div>
                    <div className="message-footer">
                        <div className="social">
                            <div className="field">
                                <i className="dripicons-thumbs-up"></i>
                                { msg.social.likes || 0 }
                            </div>
                            <div className="field">
                                <i className="dripicons-message"></i>
                                { msg.social.comments || 0 }
                            </div>
                        </div>
                        <div className="actions">
                            <button>rpondre</button>
                        </div>
                    </div>
                </div>
        });    
        return  <div className="bloc-content">
            <h4>Derniers messages</h4>
            <div className="messages">
                { messages }
            </div>
            <div className="actions">
                <button>Ajouter un message</button>
                <button>Voir tous les messages</button>
            </div>
        </div>
    }
}

function mapStateToProps(state: States.IAppState) {
    return { 
        participants : state.event.participants      
    }
}

export var BlocThematiqueWall: new (any) => React.Component<any, any> = connect(mapStateToProps)(BlocThematiqueWallComponent as any) as any;


