var INITIAL_STATE = <States.IAppUserPreferencesState>{
    socialNetworking: {
        acceptSocialNetworking : false,
        hasChosenNetworkingOrNot : false,
        socialNetworkingThemes : []
    },
    notifications : {
    }
};

export function userPreferencesReducer(state = INITIAL_STATE, action = { type: '', payload: <any>null }) {
    switch (action.type) {
        case "USER_PREFERENCES": {
            return _.assign({}, action.payload.preferences);
        }

        default:
            return state;
    }
}

export var actions = {
    setPreferences(preferences){
        return (dispatch) => {
            dispatch({ type : "USER_PREFERENCES",  payload : { preferences : preferences }});
        }
    },
}


